import React from 'react';
import { connect } from 'react-redux';
import OrderProducts from "./OrderProducts";
import ProductsTotalUtil from '../../util/ProductsTotalUtil';
import DateUtil from '../../util/DateUtil';
import { FormattedMessage } from 'react-intl';
import Roles from "../../enums/Roles";

class OrderPDF extends React.Component{

    render() {
        var ordersSettings;
        if(this.props.user.role === Roles.ADMIN) ordersSettings = this.props.ordersSettingsAdmin;
        else ordersSettings = this.props.ordersSettings;

        // Helper that will do all calculations for us
        var productsTotalUtil = new ProductsTotalUtil(this.props.order.products, this.props.products, this.props.oldProducts, null, ordersSettings);

        let color = "green";
        if(this.props.user.role === Roles.ADMIN) color = "blue";

        return (
            <div id={this.props.id} className="pdf-container">

                <div className="pdf">

                    <div>

                        <div className="w-50-perc in-block">
                            <div className="e4coll-logo"></div>
                        </div>

                        <div className="w-50-perc in-block">
                            <div className={"stamp " + color + "-bg"}>
                                COMMANDE N{this.props.order.ref} du {DateUtil.toDate(this.props.order.creation_date)}
                            </div>
                        </div>

                    </div>

                    <div>

                        <div className="w-50-perc in-block">
                            <div className="contacts">
                                <h1>Responsable Commercial</h1>
                                <p>Prnom NOM</p>
                                <p>Tel : 06.00.00.00.00</p>
                                <p>Email : nom.prenom@domain.com</p>
                                <h1>Administration des ventes</h1>
                                <p>Prnom NOM</p>
                                <p>Tel : 06.00.00.00.00</p>
                                <p>Email : nom.prenom@domain.com</p>
                            </div>
                        </div>

                        <div className="w-50-perc in-block">
                            <div className="address text-left">
                                <p>ADRESSE DE L'ETABLISSEMENT</p>
                                <p className="mention">Ordere passe par Nom Prnom</p>
                                <br/>
                                <p>00 RUE DE L'ETABLISSEMENT</p>
                                <p>0000 VILLE</p>
                            </div>
                        </div>

                    </div>

                    <br/><br/>

                    <div className={color + "-table"}>

                        <OrderProducts order={this.props.order} />

                    </div>

                    <div className="fl-fix">

                        <div className="fl-right">

                            <div className="summary">
                                <table className={color + "-bg"}>
                                    <tbody>
                                        {(productsTotalUtil.aboveMinToOrder()) &&
                                            <tr>
                                                <td><FormattedMessage id="Shipping.Cost" />&nbsp;:</td>
                                                <td><b>{productsTotalUtil.shippingCost}</b></td>
                                            </tr>
                                        }
                                        {(productsTotalUtil.urgent) &&
                                            <tr>
                                                <td><FormattedMessage id="Emergency.Cost" /></td>
                                                <td><b>{productsTotalUtil.urgent_shipping_cost}</b></td>
                                            </tr>
                                        }
                                        <tr>
                                            <td><FormattedMessage id="Total.Excl.Tax" /></td>
                                            <td><b>{productsTotalUtil.totalHt}</b></td>
                                        </tr>
                                        <tr>
                                            <td><FormattedMessage id="VAT" /></td>
                                            <td><b>{productsTotalUtil.totalTva}</b></td>
                                        </tr>
                                        <tr>
                                            <td><FormattedMessage id="Total.Incl.Tax" /></td>
                                            <td><b>{productsTotalUtil.totalTtc}</b></td>
                                        </tr>
                                    </tbody>
                                </table>
                            </div>

                            <div className="contacts">
                                <p>Dure de livraison: {ordersSettings.shipping_delay} jours</p>
                                <p>Moyens de paiment: Chque</p>
                            </div>

                        </div>

                    </div>

                </div>

            </div>
        );
    }

};

const mapStateToProps = state => {
    return {
        lang: state.i18n.lang,
        user: state.user,
        ordersSettings: state.ordersSettings,
        ordersSettingsAdmin: state.ordersSettingsAdmin,
    }
}

const mapDispatchToProps = dispatch => {
    return {
        //
    };
};

export default connect(mapStateToProps, mapDispatchToProps)(OrderPDF);
