import APIUrl from "../../APIUrl";
import axios from 'axios';

export const GET_CLIENTS = "GET_CLIENTS";

function getClientsAction(clients){return {type: GET_CLIENTS, clients: clients}};

export const getClients = function(){
    return function(dispatch){
        return axios.get(APIUrl.getClients)
            .then(function(response){
                dispatch(getClientsAction(response.data));
            })
            .catch(function(err){
                throw err;
            });
    };
};

export const addClient = function(data, successCallback){
    return function(dispatch){
        return axios.post(APIUrl.addClient, data)
            .then(function(response){
                dispatch(getClients());
                successCallback();
            })
            .catch(function(err){
                throw err;
            });
    };
};

export const updateClient = function(data){
    return function(dispatch){
        return axios.post(APIUrl.updateClient, data)
            .then(function(response){
                dispatch(getClients());
            })
            .catch(function(err){
                throw err;
            });
    };
};