import React from 'react';
import { connect } from 'react-redux';
import { FormattedMessage } from 'react-intl';
import {updateEstablishmentSettings} from '../../actions/establishments/establishmentSettings';

class EstablishmentDMRow extends React.Component {

    deleteMaterialType(field, newMaterialsTypes){
        var data = {
            establishmentId: this.props.establishmentSettings.establishment_id,
            updatedField: field,
            updatedValue: newMaterialsTypes,
        };
        this.props.onUpdateEstablishmentSettings(data);
    }

    splice(array, index){
      array.splice(index, 1);
      return array;
    }

    render() {
        // 'Delete shipping setting' modal setup
        var modalTitle = <FormattedMessage id="Confirm" />;
        var modalContent = <FormattedMessage id="Remove.Row.Confirmation" />;
        var successCallbackDelete = () => this.deleteMaterialType("materials_types", this.splice(this.props.establishmentSettings.materials_types, this.props.index));

        return (
            <React.Fragment>
                        <tr>
                            <td>{this.props.materialName}</td>
                            <td>{this.props.materialType.daily_fee}</td>
                            <td>{this.props.materialType.long_term_fee}</td>
                            <td>{this.props.materialType.maintenance_fee}</td>
                            <td>
                              <i className="fa fa-edit icon-big" onClick={(e) => this.props.openEstablishmentDMModal(this.props.index)}></i>
                              <i className="fa fa-trash icon-big" onClick={(e) => this.props.openConfModal(modalTitle, modalContent, successCallbackDelete)}></i>
                            </td>
                        </tr>
            </React.Fragment>
        );
    }

}

const mapStateToProps = state => {
    return {
        //
    }
}

const mapDispatchToProps = dispatch => {
    return {
        onUpdateEstablishmentSettings: (data) => dispatch(updateEstablishmentSettings(data)),
    };
};

export default connect(mapStateToProps, mapDispatchToProps)(EstablishmentDMRow);
