import React from 'react';
import { connect } from 'react-redux';
import {FormattedMessage} from 'react-intl';
import ResidentsModal from '../residents/ResidentsModal';
import {getResidents} from '../../actions/residents/residents';

class Fleet extends React.Component {

    constructor(props){
        super(props);

        this.state = {
            modal: null
        };
    }

    componentWillMount(){
        if(this.props.residents.length === 0) this.props.onGetResidents();
    };

    openResidentsModal(){
        this.setState({
            modal: <ResidentsModal close={() => this.closeModal()} />
        });
    }

    closeModal(){
        this.setState({modal: null});
    }

    render() {
        return (
            <React.Fragment>
                <span href="#/" className="iconed-btn hoverable clickable" onClick={(e) => this.openResidentsModal()}>
                    <span className="mt-3 mb-3">
                        <i className="fa fa-users icon-big text-dark"></i>
                        <p><FormattedMessage id="Residents.Management" /></p>
                    </span>
                </span>

                {this.state.modal}

            </React.Fragment>
        );
    }

}

const mapStateToProps = state => {
    return {
        residents: state.residents
    }
};

const mapDispatchToProps = dispatch => {
    return {
        onGetResidents: () => dispatch(getResidents())
    };
};

export default connect(mapStateToProps, mapDispatchToProps)(Fleet);
