import React from 'react';
import { connect } from 'react-redux';
import { FormattedMessage } from 'react-intl';
import ResidentModal from './ResidentModal';
import Residents from './Residents';

class ResidentsModal extends React.Component {

    constructor(props){
        super(props);

        this.state = {
            modal: null
        };
    }

    stopEvent(e){
        e.stopPropagation();
    }

    openResidentModal(){
        this.setState({
            modal: <ResidentModal close={() => this.closeModal()} />
        });
    }

    closeModal(){
        this.setState({modal: null});
    }

    render() {
        var residentsNode = null;
        
        if(this.props.residents.length === 0){
            residentsNode = (
                <React.Fragment>

                    <span href="#/" className="iconed-btn hoverable clickable" onClick={(e) => this.openResidentModal()}>
                        <span className="mt-3 mb-3">
                            <i className="fa fa-plus-circle icon-big text-dark"></i>
                            <p><FormattedMessage id="Resident.Add" /></p>
                        </span>
                    </span>

                    <div className="alert alert-secondary" role="alert">
                        <FormattedMessage id="Empty.Residents" />
                    </div>

                </React.Fragment>
            );
        }else{
            residentsNode = (
                <React.Fragment>

                    <span href="#/" className="iconed-btn hoverable clickable" onClick={(e) => this.openResidentModal()}>
                        <span className="mt-3 mb-3">
                            <i className="fa fa-plus-circle icon-big text-dark"></i>
                            <p><FormattedMessage id="Resident.Add" /></p>
                        </span>
                    </span>

                    <Residents />

                </React.Fragment>
            );
        }

        return (
            <React.Fragment>
                <div className="modal-bg show" onClick={() => this.props.close()}>
                    <div className="modal fade show">
                        <div className="modal-dialog" role="document" onClick={(e) => this.stopEvent(e)}>
                            <div className="modal-content">
                                <div className="modal-header">
                                    <h5 className="modal-title">
                                        <FormattedMessage id="Residents.Management" />
                                    </h5>
                                    <button type="button" className="close" data-dismiss="modal" aria-label="Close" onClick={() => this.props.close()}>
                                        <span aria-hidden="true">&times;</span>
                                    </button>
                                </div>

                                <div className="modal-body">
                                    
                                    {residentsNode}

                                </div>

                                <div className="modal-footer">
                                    <button type="button" className="btn btn-primary" onClick={() => this.props.close()}><FormattedMessage id="Ok" /></button>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>

                {this.state.modal}

            </React.Fragment>
        );
    }
}

const mapStateToProps = state => {
    return {
        residents: state.residents
    };
}

const mapDispatchToProps = dispatch => {
    return {
        //
    };
};

export default connect(mapStateToProps, mapDispatchToProps)(ResidentsModal);