import React from 'react';
import { connect } from 'react-redux';
import {FormattedMessage, injectIntl} from 'react-intl';
import APIUrl from '../../APIUrl';

import "../../css/families/families.css";

class Families extends React.Component {

    constructor(props){
        super(props);

        this.state = {
            search: null
        };
    }

    search(e){
        e.stopPropagation();
        e.preventDefault();

        this.setState({search: e.target.value});
    }

    render() {
        // No 'families'? No render
        if(this.props.products.length === 0){
            return <div className="alert alert-secondary" role="alert"><FormattedMessage id="Empty.Families" /></div>;
        } 

        var familiesUniques = {};
        for(let product of this.props.products ){
            familiesUniques[product.famille] = {mercurialId: product.mercurial_id, fam: product.fam};
        }

        let familiesNode = Object.keys(familiesUniques).map((famille) => {
            // Search filter
            if(this.state.search && this.state.search !== ""){
                if(famille.toUpperCase().indexOf(this.state.search.toUpperCase()) === -1) return null;
            }

            // Href link 
            var imgSrc = APIUrl.getFamilyImg + familiesUniques[famille].mercurialId + "/" + familiesUniques[famille].fam.split(".")[0];

            return (
                <div className="col-12 col-sm-6 col-md-4 col-lg-3 col-xl-2 mt-2 mb-2" key={famille}>
                    <a href={"#/home/products/" + encodeURI(famille)} className="nostyle-a">
                        <div className="card hoverable h-200">
                            <div className="h-150">
                                <img className="card-img-top img-big d-block align-middle mx-auto" 
                                    src={imgSrc} onError={(e)=>{e.target.onerror = null; e.target.src='/images/placeholder.png'}} alt="" />
                            </div>
                            <div className="card-body famille-title bg-round-grey p-2">
                                <h4 className="card-title text-center famille-title-txt">{famille}</h4>
                            </div>
                        </div>
                    </a>
                </div>
            );
        });

        let placeholder = this.props.intl.formatMessage({id: "Search.Family"});

        return (
            <React.Fragment>
                <div className="mw-600 p-3 mx-auto">
                    <input type="search" className="form-control" onChange={(e) => this.search(e)} placeholder={placeholder} />
                </div>

                <div className="row">
                    {familiesNode}
                </div>
            </React.Fragment>
        );

    }
}

const mapStateToProps = state => {
    return {
        lang: state.i18n.lang,
        products: state.products,
        mercurials: state.mercurials,
    }
}

const mapDispatchToProps = dispatch => {
    return {
     //
    };
};

export default connect(mapStateToProps, mapDispatchToProps)(injectIntl(Families));
