import React from 'react';
import { connect } from 'react-redux';
import { FormattedMessage } from 'react-intl';
import OrdersSettingsAdmin from "./ordersSettings/OrdersSettingsAdmin";
import MaterialsTypesAdmin from "./materialsTypes/MaterialsTypesAdmin";

class MenuSettingsAdmin extends React.Component {

    render() {
        if(Object.keys(this.props.ordersSettingsAdmin).length === 0) return null;

        return (
            <React.Fragment>

              <nav>
                <div className="nav nav-tabs" id="nav-tab" role="tablist">
                  <a className="nav-item nav-link p-2 pr-sm-3 pl-sm-3 rounded-0" id="nav-home-tab" data-toggle="tab" href="#nav-general" role="tab" aria-controls="nav-home" aria-selected="false"><FormattedMessage id="General" /></a>
                  <a className="nav-item nav-link active p-2 pr-sm-3 pl-sm-3 rounded-0" id="nav-profile-tab" data-toggle="tab" href="#nav-profile" role="tab" aria-controls="nav-profile" aria-selected="true"><FormattedMessage id="Orders" /></a>
                  <a className="nav-item nav-link p-2 pr-sm-3 pl-sm-3 rounded-0" id="nav-contact-tab" data-toggle="tab" href="#nav-contact" role="tab" aria-controls="nav-contact" aria-selected="false"><FormattedMessage id="Emails" /></a>
                  <a className="nav-item nav-link p-2 pr-sm-3 pl-sm-3 rounded-0" id="nav-contact-tab" data-toggle="tab" href="#nav-contact" role="tab" aria-controls="nav-contact" aria-selected="false">API</a>
                  <a className="nav-item nav-link p-2 pr-sm-3 pl-sm-3 rounded-0" id="nav-contact-tab" data-toggle="tab" href="#nav-types-dm" role="tab" aria-controls="nav-contact" aria-selected="false"><FormattedMessage id="Types.Dm" /></a>
                </div>
              </nav>
              <div className="tab-content mt-3 p-0 p-md-4" id="nav-tabContent">
                <div className="tab-pane fade" id="nav-general" role="tabpanel" aria-labelledby="nav-home-tab"></div>
                <div className="tab-pane fade show active" id="nav-profile" role="tabpanel" aria-labelledby="nav-profile-tab"><OrdersSettingsAdmin ordersSettings={this.props.ordersSettingsAdmin}/></div>
                <div className="tab-pane fade" id="nav-contact" role="tabpanel" aria-labelledby="nav-contact-tab"><FormattedMessage id="Emails" /></div>
                <div className="tab-pane fade" id="nav-contact" role="tabpanel" aria-labelledby="nav-contact-tab">api</div>
                <div className="tab-pane fade" id="nav-types-dm" role="tabpanel" aria-labelledby="nav-contact-tab"><MaterialsTypesAdmin/></div>
              </div>

            </React.Fragment>
        );
    }
}

const mapStateToProps = state => {
    return {
      ordersSettingsAdmin: state.ordersSettingsAdmin,
      user: state.user,
    }
}

const mapDispatchToProps = dispatch => {
    return {
       //
    };
};

export default connect(mapStateToProps, mapDispatchToProps)(MenuSettingsAdmin);
