import React from 'react';
import { connect } from 'react-redux';
import {FormattedMessage} from 'react-intl';
import Roles from "../../../enums/Roles";

class PromptModal extends React.Component {

    close(){
        this.props.closeModal();
    }

    confirm(){
        this.props.successCallback();
        this.close();
    }

    stopEvent(e){
		e.preventDefault();
		e.stopPropagation();
    }

    render() {
        let buttonsClassName = "btn-success";
        if(this.props.user.role === Roles.ADMIN) buttonsClassName = "btn-primary";

        return (
            <div className="modal-bg show" onClick={() => this.close()}>
                <div className="modal fade show">
                    <div className="modal-dialog" role="document" onClick={(e) => this.stopEvent(e)}>
                        <div className="modal-content">
                            <div className="modal-header">
                                <h5 className="modal-title" id="exampleModalLabel">{this.props.title}</h5>
                                <button type="button" className="close" data-dismiss="modal" aria-label="Close" onClick={() => this.close()}>
                                    <span aria-hidden="true">&times;</span>
                                </button>
                            </div>
                            <div className="modal-body">
                                <label htmlFor="comment">{this.props.content} :</label>
                                <div>
                                    <textarea className="form-control" type="text" id="comment" name="comment"/>
                                </div>
                            </div>
                            <div className="modal-footer">
                                <button type="button" className="btn btn-secondary" data-dismiss="modal" onClick={() => this.close()}><FormattedMessage id="No" /></button>
                                <button type="button" className={"btn " + buttonsClassName} onClick={() => this.confirm()}><FormattedMessage id="Yes" /></button>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}

const mapStateToProps = state => {
    return {
        user: state.user
    }
}

const mapDispatchToProps = dispatch => {
    return {
        //
    };
};

export default connect(mapStateToProps, mapDispatchToProps)(PromptModal);