import React from 'react';
import { connect } from 'react-redux';
import PDFUtil from "../../../util/PDFUtil";
import DateUtil from "../../../util/DateUtil";
import { FormattedMessage, injectIntl } from 'react-intl';
import OrderProducts from "../OrderProducts";
import { UncontrolledTooltip } from 'reactstrap';

import OrderPDF from "../OrderPDF";
import PDFViewer from "../../pdf/PDFViewer";
import OrderStatus from '../../../enums/OrderStatus';
import ProductsTotalUtil from '../../../util/ProductsTotalUtil';

import { deleteOrderAdmin, updateAdmin, duplicateAdmin } from '../../../actions/orders/orders';
import { getOldProducts } from '../../../actions/products/oldProducts';

import "../../../css/orders/orders.css";

class OrderRowAdmin extends React.Component {

    constructor(props) {
        super(props);

        this.state = {
            collapsed: false,
            displayPDF: false,
            disableExport: false,
        };

        // List of products we have already attempted to fetch from BE
        this.alreadySent = {};

        // Enabled to know if the order contains outdated products or not
        this.outdated = false;
    }

    collapse(elemId) {
        var elem = document.getElementById(elemId);

        if (elem.classList.contains("table-hidden")) {
            this.setState({ collapsed: true });
            elem.classList.remove("table-hidden");
            elem.classList.add("table-visible-blue");
        } else {
            this.setState({ collapsed: false });
            elem.classList.remove("table-visible-blue");
            elem.classList.add("table-hidden");
        }
    }

    deleteOrderAdmin(e, orderId) {
        var data = { orderId: orderId };
        this.props.onDeleteOrderAdmin(data);
    }

    togglePDF(pdfElemId, pdfFileName) {
        if(this.state.disableExport) return;
        
        if(process.env.REACT_APP_PDF_OVERVIEW === "true"){
            // Possibility n1 (dev env) : display preview of PDF (without header, footer, pages etc.)
            this.setState({displayPDF: !this.state.displayPDF});
        }else{
            // Possibility n2 (prod env) : directly save to PDF
            this.setState({disableExport: true});
            var successCallback = () => this.setState({disableExport: false});
            PDFUtil.toPDF(pdfElemId, pdfFileName, successCallback);
        }
    }

    updateStatus(e, order) {
        var data = {
            updatedValue: e.target.value,
            updatedField: "status",
            orderId: order._id
        };

        this.props.onUpdateAdmin(data);
    }

    render() {
        if(!this.props.clientName) return null;

        var order = this.props.order;

        // Use productsTotalUtil to get all totals & also automatically fetch missing products
        var productsTotalUtil = new ProductsTotalUtil(order.products, 
            this.props.products, 
            this.props.oldProducts, 
            (data) => this.props.onGetOldProducts(data));

        var missingData = productsTotalUtil.containsMissingProducts;
        var oldData = productsTotalUtil.containsOldProducts;

        // 'Delete order' modal setup
        var modalTitle = <FormattedMessage id="Confirm" />;
        var modalContent = <FormattedMessage id="Order.Remove.Confirmation" />;
        var successCallback = (e) => this.deleteOrderAdmin(e, this.props.order._id);

        let pdfElemId = "order-pdf-" + this.props.order._id;
        let pdfFileName = "order-" + this.props.order._id;
        let pdfOrder = null;
        
        if(!missingData){
            pdfOrder = (
                <OrderPDF
                    id={pdfElemId}
                    order={this.props.order}
                    products={this.props.products}
                    oldProducts={this.props.oldProducts} />
                );
        }

        var statusNode = Object.values(OrderStatus).map(option => {
            return <option key={option} value={option}>{this.props.intl.formatMessage({ id: "Order.Status." + option })}</option>
        });

        return (
            <React.Fragment>

                <tr key={this.props.order._id} className={(missingData) ? "font-italic text-muted break-word" : "break-word"}>
                    <td className="align-middle">
                        {this.props.clientName}
                        <div className="d-md-none text-muted mt-2">
                            <FormattedMessage id="Total" /> <FormattedMessage id="Incl.Tax" />&nbsp;: {productsTotalUtil.totalTtc}
                        </div>
                    </td>
                    <td className="d-none d-md-table-cell mw-200 align-middle">
                        {this.props.order.ref}
                        {(this.props.order.comment && this.props.comment !== "") && <i className="fa fa-commenting-o icon-big float-right" disabled></i>}
                    </td>
                    <td className="d-none d-md-table-cell align-middle">{DateUtil.toDateWithHour(this.props.order.creation_date)}</td>
                    <td className="d-none d-lg-table-cell align-middle">{productsTotalUtil.totalHt}</td>
                    <td className="d-none d-md-table-cell align-middle">{productsTotalUtil.totalTtc}</td>
                    <td>
                        <select className="form-control" onChange={(e) => this.updateStatus(e, this.props.order)} defaultValue={this.props.order.status} disabled={missingData}>
                            {statusNode}
                        </select>
                    </td>
                    <td className="text-center align-middle">
                        <i className={"fa fa-sort-down icon-big " + ((this.state.collapsed) ? "flip-ver" : "")}  onClick={(e) => {if(!missingData) this.collapse("prods-" + this.props.order._id)}} disabled={missingData}></i>
                        <i className="fa fa-trash icon-big" onClick={(e) => {if(!missingData) this.props.openConfModal(modalTitle, modalContent, successCallback)}} disabled={missingData}></i>
                        <i className="fa fa-cut icon-big" onClick={(e) => {if(!missingData) this.props.openSplitModal(order)}} disabled={missingData}></i>
                        <i className="fa fa-file-pdf-o icon-big" onClick={(e) => {if(!missingData) this.togglePDF(pdfElemId, pdfFileName)}} disabled={this.state.disableExport || missingData}></i>
                        {<PDFViewer open={this.state.displayPDF} toggle={(pdfElemId, pdfFileName) => {if(!missingData) this.togglePDF(pdfElemId, pdfFileName)}} template={pdfOrder} fileName={pdfFileName} />}
                        <i id={"duplicate-btn" + this.props.order._id} className="fa fa-files-o icon-big" onClick={(e) => {if(!missingData && !oldData) this.props.onDuplicateAdmin({orderId: order._id})}} disabled={missingData || oldData}></i>
                        {(oldData && !missingData) && <UncontrolledTooltip placement="top" target={"duplicate-btn" + this.props.order._id}><FormattedMessage id="Order.ToolTip.NoDuplicate" /></UncontrolledTooltip>}
                    </td>
                    <td className="text-center d-none d-lg-table-cell align-middle">
                        {(!missingData && productsTotalUtil.urgent) ? <i className={"fa fa-check icon-big text-info static"}></i> : <i className="fa fa-close icon-big text-muted op-3 static"></i>}
                    </td>
                </tr>

                <tr id={"prods-" + this.props.order._id} className="table-hidden">
                    <td colSpan="1000" className="p-1">
                        {(!missingData) && <OrderProducts order={this.props.order} />}
                    </td>
                </tr>

            </React.Fragment>
        );
    }

}

const mapStateToProps = state => {
    return {
        lang: state.i18n.lang,
        user: state.user,
        oldProducts: state.oldProducts
    }
}

const mapDispatchToProps = dispatch => {
    return {
        onDeleteOrderAdmin: (data) => dispatch(deleteOrderAdmin(data)),
        onUpdateAdmin: (data) => dispatch(updateAdmin(data)),
        onDuplicateAdmin: (data) => dispatch(duplicateAdmin(data)),
        onGetOldProducts: (data) => dispatch(getOldProducts(data))
    };
};

export default connect(mapStateToProps, mapDispatchToProps)(injectIntl(OrderRowAdmin));
