import React from 'react';
import { connect } from 'react-redux';
import {FormattedMessage} from 'react-intl';

class CartConfModalAdmin extends React.Component {

    close(){
        this.props.closeModal();
    }

    confirm(){
        this.props.successCallback();
        this.close();
    }

    stopEvent(e){
		e.preventDefault();
		e.stopPropagation();
    }

    render() {
        var clientsNode = this.props.clients.map(client => {
            return <option key={client._id} value={client._id}>{client.username}</option>
        });

        return (
            <div className="modal-bg show" onClick={() => this.close()}>
                <div className="modal fade show">
                    <div className="modal-dialog" role="document" onClick={(e) => this.stopEvent(e)}>
                        <div className="modal-content">

                            <div className="modal-header">
                                <h5 className="modal-title" id="exampleModalLabel"><FormattedMessage id="Order.Confirm" /></h5>
                                <button type="button" className="close" data-dismiss="modal" aria-label="Close" onClick={() => this.close()}>
                                    <span aria-hidden="true">&times;</span>
                                </button>
                            </div>

                            <div className="modal-body">
                                <label htmlFor="comment"><FormattedMessage id="Choose.Client" /> :</label>
                                <select id="order-clientId" className="form-control mb-3 w-100">
                                    {clientsNode}
                                </select>

                                <label htmlFor="comment"><FormattedMessage id="Comment" /> :</label>
                                <div>
                                    <textarea className="form-control" type="text" id="comment" name="comment"/>
                                </div>
                            </div>

                            <div className="modal-footer">
                            <button type="button" className="btn btn-secondary" data-dismiss="modal" onClick={() => this.close()}><FormattedMessage id="Cancel" /></button>
                                <button type="button" className="btn btn-primary" onClick={() => this.confirm()} disabled={this.props.clients.length === 0}><FormattedMessage id="Confirm" /></button>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}

const mapStateToProps = state => {
    return {
        lang: state.i18n.lang,
        user: state.user,
        clients: state.clients,
    }
}

const mapDispatchToProps = dispatch => {
    return {
        //
    };
};

export default connect(mapStateToProps, mapDispatchToProps)(CartConfModalAdmin);
