import React from 'react';
import { connect } from 'react-redux';
import {FormattedMessage} from 'react-intl';
import EstablishmentInfo from './EstablishmentInfo';
import EstablishmentConfiguration from './EstablishmentConfiguration';
import EstablishmentUsers from './EstablishmentUsers';
import EstablishmentDM from './EstablishmentDM';

class ClientsModalAdmin extends React.Component {

    close(){
        this.props.closeModal();
    }

    confirm(){
        this.props.successCallback();
        this.close();
    }

    stopEvent(e){
		e.preventDefault();
		e.stopPropagation();
    }

    getEstablishmentSettings(establishmentId){
        for(let establishmentSettings of this.props.establishmentsSettings){
            if(establishmentSettings.establishment_id === establishmentId) return establishmentSettings;
        }
    }

    getClients(establishmentId){
        var clients = [];

        for(let client of this.props.clients){
            if(client.establishment_id === establishmentId){
                clients.push(client);
            }
        }

        return clients;
    }

    render() {
        var establishmentSettings = null;
        if(this.props.establishment) establishmentSettings = this.getEstablishmentSettings(this.props.establishment._id);

        var clients = [];
        if(this.props.establishment) clients = this.getClients(this.props.establishment._id);

        // Wait for the settings object
        if(this.props.establishment && !establishmentSettings) return null;

        // If the user adds a client (no establishment), then disable the additionnal tabs
        var aClassName = (this.props.establishment) ? "" : "disabled";

        return (
            <div className="modal-bg show" onClick={() => this.close()}>
                <div className="modal fade show">
                    <div className="modal-dialog modal-xl" role="document" onClick={(e) => this.stopEvent(e)}>

                        <div className="modal-content">
                            <div className="modal-body">

                                <nav>
                                    <div className="nav nav-tabs" id="nav-tab" role="tablist">
                                    <a className="nav-item nav-link active p-2 pr-sm-3 pl-sm-3 rounded-0" id="nav-clients-info-tab" data-toggle="tab" href="#nav-clients-info"
                                        role="tab" aria-controls="nav-clients-info" aria-selected="true"><FormattedMessage id="Informations" /></a>
                                    <a className={"nav-item nav-link p-2 pr-sm-3 pl-sm-3 rounded-0 " + aClassName} id="nav-clients-settings-tab" data-toggle="tab" href="#nav-clients-settings"
                                        role="tab" aria-controls="nav-clients-settings" aria-selected="false"><FormattedMessage id="Settings" /></a>
                                    <a className={"nav-item nav-link p-2 pr-sm-3 pl-sm-3 rounded-0 " + aClassName} id="nav-clients-dmtypes-tab" data-toggle="tab" href="#nav-clients-dmtypes"
                                        role="tab" aria-controls="nav-clients-dmtypes" aria-selected="false"><FormattedMessage id="Conf.DM" /></a>
                                    <a className={"nav-item nav-link p-2 pr-sm-3 pl-sm-3 rounded-0 " + aClassName} id="nav-clients-users-tab" data-toggle="tab" href="#nav-clients-users"
                                        role="tab" aria-controls="nav-clients-users" aria-selected="false"><FormattedMessage id="Users" /></a>
                                    </div>
                                </nav>

                                <div className="tab-content mt-3 p-2" id="nav-tabContent">
                                    <div className="tab-pane fade show active" id="nav-clients-info" role="tabpanel" aria-labelledby="nav-clients-info-tab">
                                        <EstablishmentInfo
                                            establishment={this.props.establishment}
                                            openModal={(establishment) => this.props.openModal(establishment)}
                                            closeModal={() => this.props.closeModal()} />
                                    </div>

                                    <div className="tab-pane fade" id="nav-clients-settings" role="tabpanel" aria-labelledby="nav-clients-settings-tab">
                                        <EstablishmentConfiguration
                                            establishment={this.props.establishment}
                                            establishmentSettings={establishmentSettings} />
                                    </div>

                                    <div className="tab-pane fade" id="nav-clients-dmtypes" role="tabpanel" aria-labelledby="nav-clients-dmtypes-tab">
                                        <EstablishmentDM
                                            establishment={this.props.establishment}
                                            establishmentSettings={establishmentSettings}/>
                                    </div>

                                    <div className="tab-pane fade" id="nav-clients-users" role="tabpanel" aria-labelledby="nav-clients-users-tab">
                                        <EstablishmentUsers
                                            establishment={this.props.establishment}
                                            clients={clients}
                                            />
                                    </div>
                                </div>

                            </div>

                        </div>
                    </div>
                </div>
            </div>
        );
    }
}

const mapStateToProps = state => {
    return {
        establishmentsSettings: state.establishmentsSettings,
        clients: state.clients
    }
}

const mapDispatchToProps = dispatch => {
    return {
        //
    };
};

export default connect(mapStateToProps, mapDispatchToProps)(ClientsModalAdmin);
