import React from 'react';
import { connect } from 'react-redux';
import { FormattedMessage } from 'react-intl';
import MaterialTypeAdminRow from './MaterialTypeAdminRow.js';
import ConfirmationModal from '../../../sub/modals/ConfirmationModal';

class MaterialsTypesTable extends React.Component {

    constructor(props) {
        super(props);

        this.state = {
            modal: null,
        };
    }

    openConfModal(title, content, successCallback) {
        this.setState({
            modal: <ConfirmationModal isOpen={true}
                title={title}
                content={content}
                successCallback={successCallback}
                closeModal={() => this.closeModal()} />
        });
    }

    closeModal() {
        this.setState({ modal: null });
    }

    render() {
        let materialsTypesRowNode = this.props.materialsTypes.map((materialType) => {
            return <MaterialTypeAdminRow
                key={materialType._id}
                materialType={materialType}
                openConfModal={(title, content, successCallback) => this.openConfModal(title, content, successCallback)} />;

        });

        return (
            <React.Fragment>
                {this.state.modal}
                <table className="table table-striped table-bordered col-12 col-md-10 col-xl-6">
                    <thead>
                        <tr>
                            <th className="col-8"><FormattedMessage id="Type" /></th>
                            <th className="col-4"><FormattedMessage id="Actions" /></th>
                        </tr>
                    </thead>
                    <tbody>
                        {materialsTypesRowNode}
                    </tbody>
                </table>
            </React.Fragment>
        );
    }

}

const mapStateToProps = state => {
    return {
        //
    }
}

const mapDispatchToProps = dispatch => {
    return {
        //
    };
};

export default connect(mapStateToProps, mapDispatchToProps)(MaterialsTypesTable);
