import React from 'react';
import { connect } from 'react-redux';
import StringUtil from '../../util/StringUtil';
import MercurialsMapping from '../../util/MercurialsMapping';

import {FormattedMessage, injectIntl} from 'react-intl';

class MercurialColumnsModal extends React.Component {

    constructor(props){
        super(props);

        this.state = {
            disabled: false,
            autoMatching: false
        };
    }

    close() {
        this.props.closeModal();
    }

    stopEvent(e) {
        e.stopPropagation();
    }

    formatValue(value){
        let formattedValue = "" + value;
        if(formattedValue.length > 30) formattedValue = formattedValue.substring(0, 30) + "...";

        return formattedValue;
    }

    buildSelect(key){
        let firstRowKeys = Object.keys(this.props.fileData[0]);

        // Help user and pre-select the closest column (select default value)
        let closest = StringUtil.closest(key, firstRowKeys);

        var optionsNode = firstRowKeys.map(key => {
                let value = this.props.fileData[0][key];

                return <option key={key + "-" + value} value={key}>{key} - {this.formatValue(value)}</option>
            }
        );

        return (
            <select key={Math.random()} className="form-control column-select" defaultValue={(this.state.autoMatching && closest) && closest} onChange={(e) => this.manageSelectChange(e)}>
                <option value="">{this.props.intl.formatMessage({id: "Select"})}...</option>
                {optionsNode}
            </select>
        );
    }

    getSelColumn(key){
        let famDiv = document.getElementById(key);
        let select = famDiv.firstChild;
        return select.options[select.selectedIndex].value;
    }

    onComplete(){
        this.setState({disabled: true});

        var columns = {};

        for(let key of Object.keys(MercurialsMapping)){
            columns[key] = this.getSelColumn(key);
        }

        this.props.onComplete(columns);
    }

    manageSelectChange(e) {
        this.updateSelectOptions();
    }

    componentDidMount(){
        this.updateSelectOptions();
    }

    updateSelectOptions(){
        let selects = document.getElementsByClassName("column-select");

        for (let s of selects) {
            for (let i = 0; i < s.length; i++) {
                s.options[i].disabled = false;
            }
        }

        for (let s1 of selects) {
            var value1 = s1.value;

            for (let s2 of selects) {
                if (s1 === s2) continue;
                
                for (let i = 0; i < s2.length; i++) {
                    if (s2.options[i].value !== "" && s2.options[i].value === value1) s2.options[i].disabled = true;
                }
            }
        }

        this.checkDisableButton();
    }

    updateAutoMatching(){
        this.setState({autoMatching: !this.state.autoMatching}, this.updateSelectOptions);
    }

    checkDisableButton(){
        let button = document.getElementById("submit-button");
        button.disabled = (this.state.disabled || !this.selectsAllHaveValues());
    }

    selectsAllHaveValues(){
        let selects = document.getElementsByClassName("column-select");

        for (let s of selects) {
            if (!s.value || s.value === "") return false;
        }

        return true;
    }

    render() {
        // Prepare as many selects as required for the mapping
        let selects = [];
        for(let mappingKey of Object.keys(MercurialsMapping)){
            selects.push(
                <React.Fragment key={mappingKey}>
                    <label htmlFor={mappingKey} className="col-sm-4 col-form-label">{MercurialsMapping[mappingKey]}&nbsp;: </label>
                    <div id={mappingKey} className="col-sm-8 pb-3">{this.buildSelect(MercurialsMapping[mappingKey])}</div>
                </React.Fragment>
            );
        }

        // Split the selects on 2 displayed columns
        let selects1stHalf = [];
        let selects2ndHalf = [];
        for(let i = 0; i < selects.length; i++){
            if(i <= (selects.length/2)) selects1stHalf.push(selects[i]);
            else selects2ndHalf.push(selects[i]);
        }

        return (
            <div className="modal-bg show" onClick={() => this.close()}>
                <div className="modal fade show">
                    <div className="modal-dialog modal-xl" role="document" onClick={(e) => this.stopEvent(e)}>

                        <div className="modal-content">

                            <div className="modal-header">
                                <h5 className="modal-title" id="exampleModalLabel"><FormattedMessage id="Mercurials.Add.Some" /></h5>
                                
                                <button type="button" className="close" data-dismiss="modal" aria-label="Close" onClick={() => this.close()}>
                                    <span aria-hidden="true">&times;</span>
                                </button>
                            </div>

                            <div className="modal-body text-left p-4">
                                <h4 className="w-100 text-center"><FormattedMessage id="Mercurials.Step2.Desc" /></h4>
                                <h5 className="mb-4 w-100 text-center font-weight-light"><FormattedMessage id="Mercurials.Step2.Read.1st.Line" /></h5>
                                <div className="custom-control custom-switch mx-auto switch-success mb-3">
                                    <input onChange={(e) => this.updateAutoMatching()}
                                        type="checkbox"
                                        className="custom-control-input switch-bg-blue"
                                        id="auto-matching"
                                        checked={this.state.autoMatching}/>
                                    <label className="custom-control-label" htmlFor="auto-matching"><FormattedMessage id="Mercurials.Auto.Detect" /></label>
                                </div>

                                <div className="row">

                                    <div className="col-12 col-lg-6">
                                        <div className="form-group row">
                                            {selects1stHalf}
                                        </div>
                                    </div>

                                    <div className="col-12 col-lg-6">
                                        <div className="form-group row">
                                            {selects2ndHalf}
                                        </div>
                                    </div>

                                </div> 

                            </div>

                            <div className="modal-footer">
                                <button type="button" className="btn btn-secondary" data-dismiss="modal" onClick={() => this.close()}><FormattedMessage id="Cancel" /></button>
                                <button id="submit-button" type="button" className="btn btn-primary" onClick={() => this.onComplete()} disabled={this.state.disabled}>
                                    <FormattedMessage id="Mercurials.Step.3" />
                                    </button>
                            </div>

                        </div>
                    </div>
                </div>
            </div>
        );
    }
}

const mapStateToProps = state => {
    return {
        //
    }
}

const mapDispatchToProps = dispatch => {
    return {
        //
    };
};

export default connect(mapStateToProps, mapDispatchToProps)(injectIntl(MercurialColumnsModal));

