import APIUrl from "../../APIUrl";
import axios from 'axios';

export const LOGOUT = "LOGOUT";

function getLogoutAction(){return {type: LOGOUT}};

export const login = function(username, password, successCallback, failureCallback){
    return function(dispatch){
        return axios.post(APIUrl.login, {username: username, password: password})
            .then(function(response){
                return successCallback();
            })
            .catch(function(err){
                return failureCallback();
            });
    };
};

export const logout = function(successCallback){
    return function(dispatch){
        return axios.get(APIUrl.logout)
            .then(function(response){
                dispatch(getLogoutAction());
                successCallback();
            })
            .catch(function(err){
                throw(err);
            });
    };
};