import React from 'react';
import { connect } from 'react-redux';
import FileDropZone from '../sub/FileDropZone';

import {FormattedMessage} from 'react-intl';

class MercurialUpdModal extends React.Component {

    constructor(props){
        super(props);

        this.state = {
            disabled: false,
            file: null
        };
    }

    close() {
        this.props.closeModal();
    }

    stopEvent(e) {
        e.stopPropagation();
    }

    onDropFile(file){
        this.setState({file: file});
    }

    onComplete(){
        this.setState({disabled: true});

        this.props.onComplete(this.props.mercurial, this.state.file);
    }

    disableButton(){
       return (!this.state.file || this.state.disabled);
    }

    render() {
        return (
            <div className="modal-bg show" onClick={() => this.close()}>
                <div className="modal fade show">
                    <div className="modal-dialog modal-lg" role="document" onClick={(e) => this.stopEvent(e)}>

                        <div className="modal-content">

                            <div className="modal-header">
                                <h5 className="modal-title" id="exampleModalLabel"><FormattedMessage id="Mercurials.Update" /></h5>
                                
                                <button type="button" className="close" data-dismiss="modal" aria-label="Close" onClick={() => this.close()}>
                                    <span aria-hidden="true">&times;</span>
                                </button>
                            </div>

                            <div className="modal-body p-4">
                                <h4 className="mb-3 w-100 text-center"><FormattedMessage id="Mercurials.Update.Upload" /></h4>
                                <p className="mb-3 w-100 text-center font-weight-light"><FormattedMessage id="Mercurials.Update.Upload.Info" /></p>

                                <div className="card text-white bg-warning  mb-3">
                                    <div className="card-header">
                                        <i className="fa fa-warning mr-2"></i>
                                        <FormattedMessage id="Mercurials.Update.Warning.Title" />
                                    </div>
                                    <div className="card-body">
                                        <p className="card-text"><FormattedMessage id="Mercurials.Update.Warning.Content" /></p>
                                    </div>
                                </div>

                                <FileDropZone 
                                    onDropFile={(file) => this.onDropFile(file)} 
                                    acceptedExtensions={["xls", "xlsx"]}
                                    multiple={false}
                                    />

                            </div>

                            <div className="modal-footer">
                                <button type="button" className="btn btn-secondary" data-dismiss="modal" onClick={() => this.close()}><FormattedMessage id="Cancel" /></button>
                                <button type="button" className="btn btn-primary" 
                                    disabled={this.disableButton()} 
                                    onClick={() => this.onComplete()}><FormattedMessage id="Update" /></button>
                            </div>

                        </div>
                    </div>
                </div>
            </div>
        );
    }
}

const mapStateToProps = state => {
    return {
        //
    }
}

const mapDispatchToProps = dispatch => {
    return {
        //
    };
};

export default connect(mapStateToProps, mapDispatchToProps)(MercurialUpdModal);

