import React from 'react';
import { connect } from 'react-redux';
import { FormattedMessage } from 'react-intl';
import {deleteResident} from '../../actions/residents/residents';
import ConfirmationModal from '../sub/modals/ConfirmationModal';

class Residents extends React.Component {

    constructor(props) {
        super(props);

        this.state = {
            modal: null
        };
    }

    deleteResident(resident){
        var title = <FormattedMessage id="Delete.Resident" />;
        var content = <FormattedMessage id="Delete.Resident.Confirmation" />;

        this.setState({
            modal: <ConfirmationModal isOpen={true}
                title={title}
                content={content}
                successCallback={() => this.props.onDeleteResident(resident._id, () => this.closeModal())}
                closeModal={() => this.closeModal()} />
        });
    }

    closeModal(){
        this.setState({modal: null});
    }

    render() {
        let residentsNode = this.props.residents.map((resident) => {
            return (
                <tr className="break-word" key={resident._id}>
                    <td>{resident.first_name} {resident.name}</td>
                    <td className="text-center">
                        <i className="fa fa-trash icon-big" onClick={(e) => this.deleteResident(resident)}></i>
                    </td>
                </tr>
            );
        });

        return (

            <React.Fragment>

                <table className="table table-striped table-bordered">
                    <thead>
                        <tr>
                            <th><FormattedMessage id="Resident" /></th>
                            <th><FormattedMessage id="Actions" /></th>
                        </tr>
                    </thead>

                    <tbody>

                        {residentsNode}

                    </tbody>
                </table>

                {this.state.modal}

            </React.Fragment>
        );
    }

}

const mapStateToProps = state => {
    return {
        residents: state.residents
    }
}

const mapDispatchToProps = dispatch => {
    return {
        onDeleteResident: (residentId, successCallback) => dispatch(deleteResident(residentId, successCallback))
    };
};

export default connect(mapStateToProps, mapDispatchToProps)(Residents);
