import React from 'react';
import Noty from 'noty';
import { connect } from 'react-redux';
import {FormattedMessage, injectIntl} from 'react-intl';
import PriceUtil from "../../util/PriceUtil";
import Roles from "../../enums/Roles";

import "../../../node_modules/noty/lib/noty.css";
import "../../../node_modules/noty/lib/themes/bootstrap-v4.css";
import APIUrl from '../../APIUrl';

class ProductRow extends React.Component {

    constructor(props){
        super(props);

        this.state = {
            _formatId: this.props.product._id,
            disableButton: false,
            quantity : 0
        };
    }

    changeFormat(e) {
        e.preventDefault();
        e.stopPropagation();

        // Change state so that it now focuses on correct product format
        this.setState({_formatId: e.target.value});
    }

    changeQuantity(e) {
        this.setState({quantity: e.target.value});
    }

    add(e, product, quantity){
        // Disable button
        this.setState({disableButton: true});

        if(!quantity) quantity = document.getElementById("product-" + product._id).querySelector("#quantity").value;

        var successCallback = () => {
            this.setState({disableButton: false, quantity: 0});

            var designation = product.designation;
            if(designation[designation.length-1] === "-") designation = designation.slice(0, designation.length - 2);

            let notyType = 'success';
            if(this.props.user.role === Roles.ADMIN) notyType = 'info';

            new Noty({
               type: notyType,
               layout: 'topRight',
               theme: 'bootstrap-v4',
               text: this.props.intl.formatMessage({id: "Cart.Add.Success"}) + " " + designation,
               timeout: 3000,
             }).show();
        };

        // Call parent method
        this.props.onAdd(e, this.state._formatId, quantity, successCallback);
    }

    addMin(e, product){
        this.add(e, product, product.min_cde);
    }

    getProduct(id){
        // Id is main product. Return main product
        if(this.props.product._id === id) return this.props.product;

        // Id corresponds to a specific format. Use this format instead
        for(let format of this.props.product.formats){
            if(format._id === id) return format;
        }
    }
    
    getDesignation(product){
        if (!product.designation) return product.sous_famille;

        if (product.designation.indexOf('-') === 0) return product.designation;

        return product.designation.split("-")[0];
    }

    render() {
        let productFormat = this.getProduct(this.state._formatId);

        // Show a select list of the formats only if more than 1 format
        let formats = null;
        if(this.props.product.formats.length === 1){
            formats = (<div>{this.props.product.formats[0].format}</div>);
        }else{
            formats = (
                <select className="form-control" onChange={(e) => this.changeFormat(e)} defaultValue={this.state._formatId}>
                    {this.props.product.formats.map(format => 
                        <option key={format.format} value={format._id}>{format.format}</option>
                    )}
                </select>
            );
        }

        let buttonsClassName = "btn-success";
        if(this.props.user.role === Roles.ADMIN) buttonsClassName = "btn-primary";

        return (
            <tr key={this.props.product._id} id={"product-" + this.props.product._id}>
                <td className="d-none d-lg-table-cell">
                    <img className="card-img-top product-img d-block mx-auto" src={APIUrl.getProductImg + this.props.product.mercurial_id + "/" + this.props.product.ref} 
                         onError={(e)=>{e.target.onerror = null; e.target.src='/images/placeholder_120x120.png'}} alt="" />
                </td>
                <td className="product-desc">
                    <div className="product-sous-famille">{this.getDesignation(this.props.product)}</div>
                    <div className="product-caracteristiques">{this.props.product.caracteristiques}</div>
                </td>
                <td>
                    <div>
                        {formats}
                    </div>
                    <div className="product-ref">Ref: <span className="product-ref-value">{productFormat.ref}</span></div>
                    <div>{this.props.product.unite_de_vente}</div>

                    <button type="button" className={"btn " + buttonsClassName + " mt-2 rounded product-button d-md-none"}
                        disabled={this.state.disableButton}
                        onClick={(e) => this.addMin(e, this.props.product)}><FormattedMessage id="Add.Cart" /></button>
                </td>
                <td className="d-none d-md-table-cell">
                    <div><FormattedMessage id="Excl.Tax" />&nbsp;: <span className="product-ht-value">{PriceUtil.priceHt(productFormat)}</span></div>
                    <div><FormattedMessage id="Incl.Tax" />&nbsp;: <span className="product-ttc-value">{PriceUtil.priceTtc(productFormat)}</span></div>
                </td>
                <td className="d-none d-lg-table-cell">{PriceUtil.tva(this.props.product) * 100}%</td>
                <td className="d-none d-md-table-cell">
                    <div><FormattedMessage id="Quantity" />&nbsp;: </div>
                    <div><input id="quantity" type="number" value={this.state.quantity} min="0" step={this.props.product.min_cde} onChange={(e) => this.changeQuantity(e, this.props.product)} className="product-quantity-input" /></div>
                </td>
                <td className="va-middle d-none d-md-table-cell">
                    <button type="button" className={"btn " + buttonsClassName + " btn-lg btn-block rounded product-button"}
                        disabled={this.state.quantity < this.props.product.min_cde || this.state.disableButton}
                        onClick={(e) => this.add(e, this.props.product)}><FormattedMessage id="Add.Cart" /></button>
                </td>
            </tr>
        );
    }

}

const mapStateToProps = state => {
    return {
        lang: state.i18n.lang,
        user: state.user
    }
}

const mapDispatchToProps = dispatch => {
    return {
        //
    };
};

export default connect(mapStateToProps, mapDispatchToProps)(injectIntl(ProductRow));
