import React from 'react';
import { connect } from 'react-redux';
import FileDropZone from '../sub/FileDropZone';

import {FormattedMessage} from 'react-intl';

class MercurialFileModal extends React.Component {

    constructor(props){
        super(props);

        this.state = {
            disabled: false,
            file: null, 
            name: null,
            startDate: null,
            endDate: null,
            nameError: null,
            dateError: null,
        };
    }

    close() {
        this.props.closeModal();
    }

    stopEvent(e) {
        e.stopPropagation();
    }

    onDropFile(file){
        this.setState({file: file});
    }

    onNameChange(e){
        var name = e.target.value;

        for(let mercurial of this.props.mercurials){
            if(mercurial.name === name){
                this.setState({name: name, nameError: <FormattedMessage id="Mercurials.Already.Imported" />});
                return;
            } 
        }
        
        this.setState({name: name, nameError: null});
    }

    onStartDateChange(e){
        let newDate = e.target.value;

        var error = null;
        if(this.state.endDate && newDate && newDate > this.state.endDate){
            error = <FormattedMessage id="StartDate.Superior.EndDate" />
        }

        this.setState({startDate: newDate, dateError: error});       
    }

    onEndDateChange(e){
        let newDate = e.target.value;

        var error = null;
        if(this.state.startDate && newDate && this.state.startDate > newDate){
            error = <FormattedMessage id="StartDate.Superior.EndDate" />
        }

        this.setState({endDate: newDate, dateError: error});
    }

    onComplete(){
        this.setState({disabled: true});

        this.props.onComplete(this.state.name, this.state.file, this.state.startDate, this.state.endDate);
    }

    disableButton(){
       return (
           !this.state.file || 
            !this.state.name || 
            !this.state.startDate || 
            !this.state.endDate || 
            (this.state.startDate > this.state.endDate) ||
            this.state.disabled ||
            this.state.nameError
        );
    }

    render() {
        return (
            <div className="modal-bg show" onClick={() => this.close()}>
                <div className="modal fade show">
                    <div className="modal-dialog modal-lg" role="document" onClick={(e) => this.stopEvent(e)}>

                        <div className="modal-content">

                            <div className="modal-header">
                                <h5 className="modal-title" id="exampleModalLabel"><FormattedMessage id="Mercurials.Add.Some" /></h5>
                                
                                <button type="button" className="close" data-dismiss="modal" aria-label="Close" onClick={() => this.close()}>
                                    <span aria-hidden="true">&times;</span>
                                </button>
                            </div>

                            <div className="modal-body p-4">
                                <h4 className="mb-5 w-100 text-center "><FormattedMessage id="Mercurials.Step.1" /></h4>

                                <div className="form-group row">
                                    <label htmlFor="fam" className="col-sm-4 col-form-label"><FormattedMessage id="Mercurials.Name.Of" />&nbsp;: </label>
                                    <div id="fam" className="col-sm-8 pb-3">
                                        <input type="text" className="form-control" id="file-name" onChange={(e) => this.onNameChange(e)} />
                                        <div className="text-danger"><small>{this.state.nameError}</small></div>
                                    </div>

                                    <label htmlFor="file-start-date" className="col-sm-4 col-form-label"><FormattedMessage id="Start.Date" />&nbsp;: </label>
                                    <div className="col-sm-8 pb-3">
                                        <input type="date" className="form-control" id="file-start-date" onChange={(e) => this.onStartDateChange(e)} />
                                        <div className="text-danger"><small>{this.state.dateError}</small></div>
                                    </div>

                                    <label htmlFor="file-end-date" className="col-sm-4 col-form-label"><FormattedMessage id="End.Date" />&nbsp;: </label>
                                    <div className="col-sm-8 pb-3">
                                        <input type="date" className="form-control" id="file-end-date" onChange={(e) => this.onEndDateChange(e)} />
                                    </div>
                                </div>

                                <FileDropZone 
                                    onDropFile={(file) => this.onDropFile(file)} 
                                    acceptedExtensions={["xls", "xlsx"]}
                                    multiple={false}
                                    />

                                <div className="w-100 text-center mt-3">
                                    <a role="button" href="templates/template_mercurial.xlsx" download="template_mercurial.xlsx">
                                        <i className="fa fa-file-excel-o text-blue mr-2"></i>
                                        
                                        <FormattedMessage id="Mercurials.Download.Template" />
                                    </a>
                                </div>

                            </div>

                            <div className="modal-footer">
                                <button type="button" className="btn btn-secondary" data-dismiss="modal" onClick={() => this.close()}><FormattedMessage id="Cancel" /></button>
                                <button type="button" className="btn btn-primary" 
                                    disabled={this.disableButton()} 
                                    onClick={() => this.onComplete()}><FormattedMessage id="Mercurials.Step.2" /></button>
                            </div>

                        </div>
                    </div>
                </div>
            </div>
        );
    }
}

const mapStateToProps = state => {
    return {
        mercurials: state.mercurials
    }
}

const mapDispatchToProps = dispatch => {
    return {
        //
    };
};

export default connect(mapStateToProps, mapDispatchToProps)(MercurialFileModal);

