import React from 'react';
import { connect } from 'react-redux';
import { FormattedMessage } from 'react-intl';
import Roles from "../../enums/Roles";
import PDFUtil from "../../util/PDFUtil";

class PDFViewer extends React.Component{

    constructor(props){
        super(props);

        this.state = {
            open: true
        };
    }

    close(e){
        e.preventDefault();
        e.stopPropagation();

        this.setState({open: false});
    }

    render(){
        // The viewer is not really 'closed', because for PDF export the element needs to be in the HTML.
        // We just hide it very far away.
        if(!this.props.open){
            return (
                <div style={{position: "absolute", marginLeft: "-10000px"}}>
                    {this.props.template}
                </div>
            );
        }

        let buttonsClassName = "pdf-overview-green-btn";
        if(this.props.user.role === Roles.ADMIN) buttonsClassName = "pdf-overview-blue-btn";

        return (
            <div className="pdf-overview-bg" onClick={(e) => this.props.toggle(e)}>

                <button 
                    className={"pdf-overview-export-button " + buttonsClassName}
                    onClick={(e) => PDFUtil.toPDF(this.props.template.props.id, this.props.fileName)}>
                    <FormattedMessage id="Export.PDF" />
                </button>

                <div className="pdf-overview">
                    {this.props.template}
                </div>

            </div>
        );
    }

};

const mapStateToProps = state => {
    return {
        lang: state.i18n.lang,
        user: state.user
    }
}

const mapDispatchToProps = dispatch => {
    return {
        //
    };
};

export default connect(mapStateToProps, mapDispatchToProps)(PDFViewer);