import React from 'react';
import { connect } from 'react-redux';
import { FormattedMessage } from 'react-intl';
import UserModal from './UserModal';

class EstablishmentUsers extends React.Component {

    constructor(props) {
        super(props);

        this.state = {
            modal: null
        };
    }

    openModal(user){
        this.setState({
            modal: (
                <UserModal 
                    user={user}
                    establishment={this.props.establishment}
                    close={() => this.closeModal()} />
            )
        });
    }

    closeModal(){
        this.setState({modal: null});
    }

    render() {
        let clientsNode = this.props.clients.map((client) => {
            return (
                <tr className="break-word" key={client._id}>
                    <td className="d-none d-lg-table-cell">{client.username}</td>
                    <td>{client.name}</td>
                    <td>{client.first_name}</td>
                    <td className="d-none d-lg-table-cell">{client.email}</td>
                    <td className="d-none d-lg-table-cell">{client.function}</td>
                    <td className="d-none d-md-table-cell text-center">{(client.validator) ? <i className="fa fa-check"></i> : <i className="fa fa-close"></i>}</td>
                    <td className="text-center">
                        <i className="fa fa-edit icon-big" onClick={(e) => this.openModal(client)}></i>
                    </td>
                </tr>
            );
        });

        let tableStyle = "table table-striped table-bordered";
        if(this.props.cssStyle) tableStyle = this.props.cssStyle;

        return (

            <React.Fragment>
                <table className={tableStyle}>
                    <thead>
                        <tr>
                            <th className="d-none d-lg-table-cell"><FormattedMessage id="Username" /></th>
                            <th className=""><FormattedMessage id="Name" /></th>
                            <th className=""><FormattedMessage id="First.Name" /></th>
                            <th className="d-none d-lg-table-cell"><FormattedMessage id="Email" /></th>
                            <th className="d-none d-lg-table-cell"><FormattedMessage id="Function" /></th>
                            <th className="d-none d-md-table-cell"><FormattedMessage id="Validator" /></th>
                            <th><FormattedMessage id="Actions" /></th>
                        </tr>
                    </thead>
                    <tbody>

                        {clientsNode}

                    </tbody>
                </table>

                {this.state.modal}
            </React.Fragment>
        );
    }

}

const mapStateToProps = state => {
    return {
        //
    }
}

const mapDispatchToProps = dispatch => {
    return {
        //
    };
};

export default connect(mapStateToProps, mapDispatchToProps)(EstablishmentUsers);
