import React from 'react';
import { connect } from 'react-redux';
import { FormattedMessage } from 'react-intl';
import Util from '../../util/Util';
import {addResident, checkIfResidentExists} from '../../actions/residents/residents';

class ResidentModal extends React.Component {

    constructor(props){
        super(props);

        this.state = {
            name: "",
            firstname: "",

            // Errors
            uniqueError: null,

            disabled: false,
        };
    }

    stopEvent(e){
        e.stopPropagation();
    }

    checkUniqueness(name, firstname, noErrorCallback){
        // Make sure the name / first name combination is unique
        this.props.onCheckIfResidentExists(
            {name: name, firstname: firstname},
            // Resident exists. Return error
            () => {this.setState({uniqueError: <FormattedMessage id="Resident.Already.Exists" />})},
            // Resident does not exist!
            () => noErrorCallback()
        );
    }

    onComplete(){
        var createResident = () => {
            var resident = {
                name: this.state.name,
                firstname: this.state.firstname
            };
    
            this.setState({disabled: true});
    
            this.props.onAddResident(resident, () => this.props.close());
        };

        // Validate fields & send to BE
        this.checkUniqueness(this.state.name, this.state.firstname, createResident);
    }

    onFieldChange(field, value) {
        this.setState({ [field]: value });
    }

    disabled(){
        return (Util.emptyString(this.state.name) || Util.emptyString(this.state.firstname) || this.state.disabled);
    }

    render() {
        return (
            <div className="modal-bg show" onClick={() => this.props.close()}>
                <div className="modal fade show">
                    <div className="modal-dialog" role="document" onClick={(e) => this.stopEvent(e)}>
                        <div className="modal-content">
                            <div className="modal-header">
                                <h5 className="modal-title">
                                    <FormattedMessage id="Resident.Add" />
                                </h5>
                                <button type="button" className="close" data-dismiss="modal" aria-label="Close" onClick={() => this.props.close()}>
                                    <span aria-hidden="true">&times;</span>
                                </button>
                            </div>

                            <div className="modal-body">

                                <div className="form-group row">
                                    <label htmlFor="resident-name" className="col-12 col-md-5 col-form-label"><FormattedMessage id="Name" /></label>
                                    <div className="col-12 col-md-7">
                                        <input type="text" className="form-control" id="resident-name" autoComplete="off"
                                            value={this.state.name}
                                            onChange={(e) => this.onFieldChange("name", e.target.value)} />
                                    </div>
                                </div>

                                <div className="form-group row">
                                    <label htmlFor="resident-firstname" className="col-12 col-md-5 col-form-label"><FormattedMessage id="First.Name" /></label>
                                    <div className="col-12 col-md-7">
                                        <input type="text" className="form-control" id="resident-firstname" autoComplete="off"
                                            value={this.state.firstname}
                                            onChange={(e) => this.onFieldChange("firstname", e.target.value)} />
                                        <div className="text-danger"><small>{this.state.uniqueError}</small></div>
                                    </div>
                                </div>

                            </div>

                            <div className="modal-footer">
                                <button type="button" className="btn btn-secondary" data-dismiss="modal" onClick={() => this.props.close()}><FormattedMessage id="Cancel" /></button>
                                <button type="button" className="btn btn-primary" disabled={this.disabled()} onClick={() => this.onComplete()}><FormattedMessage id="Add" /></button>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}

const mapStateToProps = state => {
    return {
        //
    };
}

const mapDispatchToProps = dispatch => {
    return {
        onAddResident: (resident, successCallback) => dispatch(addResident(resident, successCallback)),
        onCheckIfResidentExists: (resident, existsCallback, noExistsCallback) => dispatch(checkIfResidentExists(resident, existsCallback, noExistsCallback)),
    };
};

export default connect(mapStateToProps, mapDispatchToProps)(ResidentModal);