import React from 'react';
import { connect } from 'react-redux';
import Users from './Users';
import { FormattedMessage } from 'react-intl';
import ProductsTotalUtil from "../../util/ProductsTotalUtil";
import { getOldProducts } from '../../actions/products/oldProducts';

class EstablishmentsRow extends React.Component {

    constructor(props) {
        super(props);

        this.state = {
            modal: null
        };
    }

    getMercurialName(establishmentId){
        var settings = null;
        for(let s of this.props.establishmentsSettings){
            if(s.establishment_id === establishmentId){
                settings = s;
                break;
            }
        }

        // If no settings, or mercurial wasn't defined yet
        if(!settings || !settings.mercurial_id) return null;

        for(let m of this.props.mercurials){
            if(m._id === settings.mercurial_id){
                let version = "";
                if(m.version !== 1) version = "(v" + m.version + ")";
                return m.name + " " + version;
            } 
        }
    }

    collapse(elemId){
        var elem = document.getElementById(elemId);

        if(elem.classList.contains("table-hidden")){
            this.setState({collapsed: true});
            elem.classList.remove("table-hidden");
            elem.classList.add("table-visible-blue");
        }else{
            this.setState({collapsed: false});
            elem.classList.remove("table-visible-blue");
            elem.classList.add("table-hidden");
        }
    }

    getTotalHT(establishmentId){
        // First, find out all the orders from the establishment
        var orders = [];
        for(let client of this.props.clients){
            // Client does not belong to the establishment
            if(client.establishment_id !== establishmentId) continue;

            for(let order of this.props.orders){
                if(order.id_client === client._id) orders.push(order);
            }
        }

        var totalHT = 0;
        for(let order of orders){
            totalHT += this.getCmdTotalHT(order);
        }

        return totalHT;
    }

    getCmdTotalHT(order){
        var productsTotalUtil = new ProductsTotalUtil(order.products, this.props.products, this.props.oldProducts, (data) => this.props.onGetOldProducts(data));
        
        return productsTotalUtil.totalHt;
    }

    render() {
        var establishment = this.props.establishment;

        return (
            <React.Fragment>

                <tr key={establishment._id}>
                    <td>{establishment.name}</td>
                    <td className="d-none d-md-table-cell">{establishment.address}</td>
                    <td className="d-none d-md-table-cell">{this.getMercurialName(establishment._id)}</td>
                    <td className="d-none d-sm-table-cell">{this.getTotalHT(establishment._id)}</td>
                    <td className="d-none d-md-table-cell">XXX</td>
                    <td className="d-none d-md-table-cell">XXX</td>
                    <td className="d-none d-sm-table-cell">XXXXXX</td>
                    <td className="text-center">
                        <i className={"fa fa-sort-down icon-big " + ((this.state.collapsed) ? "flip-ver" : "")}  onClick={(e) => this.collapse("client-" + establishment._id)} disabled={false}></i>
                        <i className="fa fa-edit icon-big" onClick={(e) => this.props.openModal(establishment)}></i>
                    </td>
                </tr>

                <tr id={"client-" + establishment._id} className="table-hidden">
                    <td colSpan="1000" className="p-1">
                        {(this.props.clients.length > 0) ? 
                            <Users clients={this.props.clients} cssStyle={"table table-striped table-bordered m-0"} /> :
                                <div className="alert alert-secondary m-0" role="alert"><FormattedMessage id="Empty.Users" /></div>}
                    </td>
                </tr>

            </React.Fragment>
        );
    }

}

const mapStateToProps = state => {
    return {
        establishmentsSettings: state.establishmentsSettings,
        mercurials: state.mercurials,
        products: state.products,
        oldProducts: state.oldProducts,
        orders: state.orders
    }
};

const mapDispatchToProps = dispatch => {
    return {
        onGetOldProducts: (data) => dispatch(getOldProducts(data))
    };
};

export default connect(mapStateToProps, mapDispatchToProps)(EstablishmentsRow);
