import APIUrl from "../../../APIUrl";
import axios from 'axios';
import {getEstablishmentSettings} from "../../establishments/establishmentSettings";

export const GET_MATERIALS_TYPES = "GET_MATERIALS_TYPES";

function getMaterialsTypesAction(materialsTypes){return {type: GET_MATERIALS_TYPES, materialsTypes: materialsTypes}};

export const getMaterialsTypes = function(){
    return function(dispatch){
        return axios.get(APIUrl.getMaterialsTypes)
            .then(function(response){
                dispatch(getMaterialsTypesAction(response.data));
            })
            .catch(function(err){
                throw(err);
            });
    };
};

export const addMaterialsType = function(data, successCallback){
    return function(dispatch){
        return axios.post(APIUrl.addMaterialsType, data)
            .then(function(response){
                dispatch(getMaterialsTypes());
                successCallback();
            })
            .catch(function(err){
                throw(err);
            });
    };
};

export const deleteMaterialType = function(data){
    return function(dispatch){
        return axios.post(APIUrl.deleteMaterialType, data)
            .then(function(response){
                dispatch(getMaterialsTypes());
                dispatch(getEstablishmentSettings());
            })
            .catch(function(err){
                throw(err);
            });
    };
};
