import React from 'react';
import { connect } from 'react-redux';
import { FormattedMessage } from 'react-intl';
import ConfirmationModal from '../sub/modals/ConfirmationModal';
import EstablishmentDMModal from './EstablishmentDMModal';
import EstablishmentDMRow from './EstablishmentDMRow'

class EstablishmentDMTable extends React.Component {

    constructor(props) {
        super(props);

        this.state = {
            modal: null,
        };
    }

    openEstablishmentDMModal(index) {
      this.setState({
        modal: <EstablishmentDMModal
          isOpen={true}
          index={index}
          establishment={this.props.establishment}
          establishmentSettings={this.props.establishmentSettings}
          close={() => this.closeModal()} />
      });
    }

    openConfModal(title, content, successCallback) {
        this.setState({
            modal: <ConfirmationModal isOpen={true}
                title={title}
                content={content}
                successCallback={successCallback}
                closeModal={() => this.closeModal()} />
        });
    }

    closeModal() {
        this.setState({ modal: null });
    }

    getMaterialName(materialTypeId){
        for(var materialType of this.props.materialsTypes){
          if(materialType._id === materialTypeId) return materialType.name;
        }
    }

    render() {
        var index = -1;
        let establishmentDMRowNode = this.props.establishmentSettings.materials_types.map((materialType) => {
            index++
            return <EstablishmentDMRow
                      key={materialType.material_type_id}
                      index={index}
                      establishmentSettings={this.props.establishmentSettings}
                      openConfModal={(title, content, successCallback) => this.openConfModal(title, content, successCallback)}
                      openEstablishmentDMModal={(index) => this.openEstablishmentDMModal(index)}
                      materialType={materialType}
                      materialName={this.getMaterialName(materialType.material_type_id)}/>;
        });

        return (
            <React.Fragment>
                <table className="table table-striped table-bordered col-12">
                    <thead>
                        <tr>
                            <th><FormattedMessage id="Type" /></th>
                            <th><FormattedMessage id="Daily.Fee" /></th>
                            <th><FormattedMessage id="Long.Term.Fee" /></th>
                            <th><FormattedMessage id="Maintenance.Fee" /></th>
                            <th><FormattedMessage id="Actions" /></th>
                        </tr>
                    </thead>
                    <tbody>
                      {establishmentDMRowNode}
                    </tbody>
                </table>
                {this.state.modal}
            </React.Fragment>
        );
    }

}

const mapStateToProps = state => {
    return {
        materialsTypes: state.materialsTypes,
    }
}

const mapDispatchToProps = dispatch => {
    return {
        //
    };
};

export default connect(mapStateToProps, mapDispatchToProps)(EstablishmentDMTable);
