import React from 'react';
import { connect } from 'react-redux';
import { addProductToCart } from '../../actions/cart/cart';
import {addProductToCartAdmin} from '../../actions/cart/cart';
import Maths from "../../util/Maths";
import Util from "../../util/Util";
import ProductRow from "./ProductRow";
import { FormattedMessage, injectIntl } from 'react-intl';
import Roles from '../../enums/Roles';

import "../../css/products/products.css";

class Products extends React.Component {

    constructor(props) {
        super(props);

        this.state = {
            search: null
        };
    }

    getFormat(product) {
        if (!product.designation) return null;

        if (product.designation.indexOf('-') === 0) return null;

        let substrings = product.designation.split("-");
        return substrings[substrings.length - 1];
    }

    equal(product1, product2) {
        let isolatedDesignation1 = product1.designation.replace(this.getFormat(product1), "").trim();
        let isolatedDesignation2 = product2.designation.replace(this.getFormat(product2), "").trim();

        return (product1.famille === product2.famille
            && product1.sous_famille === product2.sous_famille
            && product1.caracteristiques === product2.caracteristiques
            && isolatedDesignation1 === isolatedDesignation2);
    }

    parseProducts(products, famille) {
        let newProducts = [];

        outer: for (let p1 of products) {
            if (p1.famille !== famille) continue;

            let p = Util.shallowClone(p1);

            // Make sure p1 is not already in newProducts
            for (let n of newProducts) {
                if (this.equal(p, n)) continue outer;
            }

            let formats = {};
            let formatP = this.getFormat(p);
            formats[formatP] = {  _id: p1._id, format: formatP, ref: p.ref, prix_u_ht_emera: p.prix_u_ht_emera, tva: p.tva };
            
            // Get all formats for identical products
            for (let p2 of products) {
                if (this.equal(p, p2)){
                    let formatP2 = this.getFormat(p2);
                    if(!formats[formatP2]) formats[formatP2] = { _id: p2._id, format: formatP2, ref: p2.ref, prix_u_ht_emera: p2.prix_u_ht_emera, tva: p2.tva };
                } 
            }

            // Insert product with reformatted designation and formats
            p.designation = p.designation.replace(formatP, "");
            p.formats = Object.values(formats);
            newProducts.push(p);
        }

        return newProducts;
    }

    changeFormat(e, product) {
        e.preventDefault();
        e.stopPropagation();

        for (let format of product.formats) {
            if (format.format === e.target.value) {
                let htmlObj = document.getElementById("product-" + product._id);

                htmlObj.getElementsByClassName("product-ref-value")[0].innerHTML = format.ref;
                htmlObj.getElementsByClassName("product-ht-value")[0].innerHTML = Maths.round(format.prix_u_ht_emera);
                htmlObj.getElementsByClassName("product-ttc-value")[0].innerHTML = Maths.round(format.prix_u_ht_emera * (1 + parseFloat(format.tva)));
                return;
            }
        }
    }

    add(e, productId, quantity, successCallback) {
        var data = {
            id_product: productId,
            quantity: quantity,
        };

        // Add product. Careful with role
        if(this.props.user.role === Roles.ADMIN) this.props.onAddProductToCartAdmin(data, successCallback);
        else this.props.onAddProductToCart(data, successCallback);
    }

    search(e) {
        e.stopPropagation();
        e.preventDefault();

        this.setState({ search: e.target.value });
    }

    render() {
        // No 'families'? No render
        if (this.props.products.length === 0) return null;

        var famille = this.props.match.params.famille;

        // Make a special treatment to raw products. Regroup by categories, for instance
        let products = this.parseProducts(this.props.products, famille);

        let productsNode = products.map((product) => {
            if (product.famille !== famille) return null;

            // Search filter
            if (this.state.search && this.state.search !== "") {
                if (product.sous_famille.toUpperCase().indexOf(this.state.search.toUpperCase()) === -1 
                    && product.caracteristiques.toUpperCase().indexOf(this.state.search.toUpperCase()) === -1) return null;
            }

            return <ProductRow product={product} key={product._id} onAdd={(e, productId, quantity, successCallback) => this.add(e, productId, quantity, successCallback)} />
        });

        let placeholder = this.props.intl.formatMessage({ id: "Search.Product" });

        return (
            <React.Fragment>
                <div>
                    <span className="float-left p-2">
                        <a href="#/home/families" className="d-inline-block align-top iconed-btn nostyle-a hoverable m-0">
                            <FormattedMessage id="Categories" />
                        </a>
                        <div className="d-inline-block align-top pr-2">/</div>
                        <span className="d-inline-block align-top iconed-btn m-0">
                            {famille}
                        </span>
                    </span>

                    <div className="mw-600 p-3 mx-auto">
                        <input type="search" className="form-control" onChange={(e) => this.search(e)} placeholder={placeholder} />
                    </div>
                </div>

                <table className="table table-striped table-bordered">
                    <thead>
                        <tr>
                            <th className="d-none d-lg-table-cell"><FormattedMessage id="Product" /></th>
                            <th><FormattedMessage id="Description" /></th>
                            <th><FormattedMessage id="Format" /></th>
                            <th className="d-none d-md-table-cell"><FormattedMessage id="Unit.Price" /></th>
                            <th className="d-none d-lg-table-cell"><FormattedMessage id="VAT" /></th>
                            <th className="d-none d-md-table-cell"><FormattedMessage id="Quantity" /></th>
                            <th className="d-none d-md-table-cell"></th>
                        </tr>
                    </thead>
                    <tbody>
                        {productsNode}
                    </tbody>
                </table>
            </React.Fragment>
        );

    }
}

const mapStateToProps = state => {
    return {
        user: state.user,
        products: state.products
    }
}

const mapDispatchToProps = dispatch => {
    return {
        onAddProductToCart: (data, successCallback) => dispatch(addProductToCart(data, successCallback)),
        onAddProductToCartAdmin: (data, successCallback) => dispatch(addProductToCartAdmin(data, successCallback))
    };
};

export default connect(mapStateToProps, mapDispatchToProps)(injectIntl(Products));
