import React from 'react';
import { connect } from 'react-redux';
import { FormattedMessage } from 'react-intl';
import { withScriptjs, withGoogleMap, GoogleMap, Marker } from "react-google-maps";
import {addEstablishment, updateEstablishment} from '../../actions/establishments/establishments';
import {addEstablishmentType} from '../../actions/configurables/establishmentTypes';
import ComboxBox from "../sub/ComboBox";
import Util from '../../util/Util';
// import Geocode from "react-geocode";

// import axios from 'axios';

class EstablishmentInfo extends React.Component {

    constructor(props) {
        super(props);
        
        var establishment = this.props.establishment || {};

        this.state = {
            name: establishment.name || "",
            email: establishment.email || "",
            phone: establishment.phone || "",
            address: establishment.address || "",
            address_compl: establishment.address_compl || "",
            postal_code: establishment.postal_code || "",
            city: establishment.city || "",
            website: establishment.website || "",
            type_id: establishment.type_id || "",

            nameError: null,
            disabled: false
        };

        this.GOOGLE_MAP_API_KEY = process.env.REACT_APP_GOOGLE_MAP_API_KEY;

        // Default coords : Paris
        var defaultLat = 48.8534;
        var defaultLong = 2.3488;
        this.mapComponent = withScriptjs(withGoogleMap((props) =>
            <GoogleMap defaultZoom={5} defaultCenter={{ lat: defaultLat, lng: defaultLong }}>
                {props.isMarkerShown && <Marker position={{ lat: defaultLat, lng: defaultLong }} />}
            </GoogleMap>
        ));

        // Geocode (for geocoding, no kidding)
        // Geocode.setApiKey(this.GOOGLE_MAP_API_KEY);

        // Geocode.fromAddress("Paris").then(function (response) {
        //     const { lat, lng } = response.results[0].geometry.location;
        //     console.log(lat, lng);
        // }, function (error) {
        //     console.error(error);
        // });

        // axios.get("https://maps.google.com/maps/api/geocode/json?address=Paris&key=AIzaSyBxMSAT2ijMVaod_XWLk2eto1h3B8_mlSA&sensor=true")
        //     .then(function(response){
        //         console.log(response)
        //     })
        //     .catch(function(err){
        //         throw(err);
        //     });
    }

    onFieldChange(field, value) {
        this.setState({ [field]: value });

        // Careful with name : must be unique! 
        if(field === "name"){
            if(!this.isNameUnique(value)){
                this.setState({nameError: <FormattedMessage id="Establishment.Name.Already.Exists" />});
                return;
            }else{
                this.setState({ nameError: null });
            }
        }
    }

    sendUpdate(field, value){
        // Establishment already exists, update field value
        if(this.props.establishment && !this.disabled()){
            this.props.onUpdateEstablishment({establishmentId: this.props.establishment._id, updatedField: field, updatedValue: value});
        }
    }

    isNameUnique(name){
        for(let establishment of this.props.establishments){
            if(establishment.name.toLowerCase() === name.toLowerCase()){
                // Careful: same establishment we're modifying, so name conflict doesn't matter
                if(this.props.establishment && this.props.establishment._id === establishment._id) continue;
                return false;
            }
        }

        return true;
    }

    onAddressBlur() {
        if(!this.state.address) return;

        // Geocode.fromAddress(this.state.address).then(function (response) {
        //     const { lat, lng } = response.results[0].geometry.location;
        //     console.log(lat, lng);
        // }, function (error) {
        //     console.error(error);
        // });
    }

    onSubmit() {
        var establishment = {
            name: this.state.name,
            email: this.state.email,
            phone: this.state.phone,
            address: this.state.address,
            address_compl: this.state.address_compl,
            postal_code: this.state.postal_code,
            city: this.state.city,
            website: this.state.website,
            type_id: this.state.type_id
        };

        // Close the modal and immediately open it back, parametrized with the establishment
        var successCallback = (esta) => {
            this.props.closeModal();
            this.props.openModal(esta);
        };

        this.props.onAddEstablishment(establishment, successCallback);
    }

    disabled() {
        return (Util.emptyString(this.state.name) ||
            !this.isNameUnique(this.state.name) ||
            this.state.disabled);
    }

    render() {
        return (
            <React.Fragment>

                <div className="row mb-3">

                    <div className="col-12 col-lg-6">

                        <div className="form-group row">
                            <label htmlFor="establishment-name" className="col-12 col-md-5 col-form-label"><FormattedMessage id="Establishment" /></label>
                            <div className="col-12 col-md-7">
                                <input type="text" className="form-control" id="establishment-name" autoComplete="off"
                                    value={this.state.name}
                                    onChange={(e) => this.onFieldChange("name", e.target.value)}
                                    onBlur={(e) => this.sendUpdate("name", e.target.value)} />
                                {(this.state.nameError) && <div className="text-danger"><small>{this.state.nameError}</small></div>}
                            </div>
                        </div>
                        <div className="form-group row">
                            <label htmlFor="establishment-email" className="col-12 col-md-5 col-form-label"><FormattedMessage id="Email" /></label>
                            <div className="col-12 col-md-7">
                                <input type="email" className="form-control" id="establishment-email" autoComplete="off"
                                    value={this.state.email}
                                    onChange={(e) => this.onFieldChange("email", e.target.value)}
                                    onBlur={(e) => this.sendUpdate("email", e.target.value)} />
                            </div>
                        </div>
                        <div className="form-group row">
                            <label htmlFor="establishment-phone" className="col-12 col-md-5 col-form-label"><FormattedMessage id="Phone" /></label>
                            <div className="col-12 col-md-7">
                                <input type="text" className="form-control" id="establishment-phone" autoComplete="off"
                                    value={this.state.phone}
                                    onChange={(e) => this.onFieldChange("phone", e.target.value)}
                                    onBlur={(e) => this.sendUpdate("phone", e.target.value)} />
                            </div>
                        </div>
                        <div className="form-group row">
                            <label htmlFor="establishment-address" className="col-12 col-md-5 col-form-label"><FormattedMessage id="Address" /></label>
                            <div className="col-12 col-md-7">
                                <input type="text" className="form-control" id="establishment-address" autoComplete="off"
                                    value={this.state.address}
                                    onChange={(e) => this.onFieldChange("address", e.target.value)}
                                    onBlur={(e) => this.sendUpdate("address", e.target.value)} />
                            </div>
                        </div>
                        <div className="form-group row">
                            <label htmlFor="establishment-address-compl" className="col-12 col-md-5 col-form-label"><FormattedMessage id="Address.Compl" /></label>
                            <div className="col-12 col-md-7">
                                <input type="text" className="form-control" id="establishment-address-compl" autoComplete="off"
                                    value={this.state.address_compl}
                                    onChange={(e) => this.onFieldChange("address_compl", e.target.value)}
                                    onBlur={(e) => this.sendUpdate("address_compl", e.target.value)} />
                            </div>
                        </div>
                        <div className="form-group row">
                            <label htmlFor="establishment-postal-code" className="col-12 col-md-5 col-form-label"><FormattedMessage id="Postal.Code" /></label>
                            <div className="col-12 col-md-7">
                                <input type="number" className="form-control" id="establishment-postal-code" autoComplete="off"
                                    value={this.state.postal_code}
                                    onChange={(e) => this.onFieldChange("postal_code", e.target.value)}
                                    onBlur={(e) => this.sendUpdate("postal_code", e.target.value)} />
                            </div>
                        </div>
                        <div className="form-group row">
                            <label htmlFor="establishment-city" className="col-12 col-md-5 col-form-label"><FormattedMessage id="City" /></label>
                            <div className="col-12 col-md-7">
                                <input type="text" className="form-control" id="establishment-city" autoComplete="off"
                                    value={this.state.city}
                                    onChange={(e) => this.onFieldChange("city", e.target.value)}
                                    onBlur={(e) => this.sendUpdate("city", e.target.value)} />
                            </div>
                        </div>
                        <div className="form-group row">
                            <label htmlFor="establishment-website" className="col-12 col-md-5 col-form-label"><FormattedMessage id="Website" /></label>
                            <div className="col-12 col-md-7">
                                <input type="text" className="form-control" id="establishment-city" autoComplete="off"
                                    value={this.state.website}
                                    onChange={(e) => this.onFieldChange("website", e.target.value)}
                                    onBlur={(e) => this.sendUpdate("website", e.target.value)} />
                            </div>
                        </div>

                        <div className="form-group row">
                            <label htmlFor="establishment-type-id" className="col-12 col-md-5 col-form-label"><FormattedMessage id="Type" /></label>
                            <div id="establishment-type-id" className="col-12 col-md-7">
                                <ComboxBox 
                                    menuPlacement="top"
                                    onChange={(value) => this.onFieldChange("type_id", value)}
                                    onBlur={(value) => this.sendUpdate("type_id", value)}
                                    defaultOption={this.state.type_id}
                                    options={this.props.establishmentTypes}
                                    onCreateOption={(data, successCallback) => this.props.onAddEstablishmentType(data, successCallback)} />
                            </div>
                        </div>

                    </div>

                    <div className="col-12 col-lg-6">
                        <this.mapComponent
                            isMarkerShown
                            googleMapURL={"https://maps.googleapis.com/maps/api/js?key=" + this.GOOGLE_MAP_API_KEY + "&v=3.exp&libraries=geometry,drawing,places"}
                            loadingElement={<div style={{ height: `100%` }} />}
                            containerElement={<div style={{ height: `400px` }} />}
                            mapElement={<div style={{ height: `100%` }} />}
                        />
                    </div>

                </div>

                <div className="row">

                    {(!this.props.establishment) && <div className="col-12 col-lg-6 offset-lg-3">
                        <button className="btn btn-primary btn-block" disabled={this.disabled()} onClick={() => this.onSubmit()}>
                            <FormattedMessage id="Add" />
                        </button>
                    </div>}

                </div>

            </React.Fragment>
        );
    }

}

const mapStateToProps = state => {
    return {
        establishments: state.establishments,
        establishmentTypes: state.establishmentTypes
    }
}

const mapDispatchToProps = dispatch => {
    return {
        onAddEstablishment: (data, successCallback) => dispatch(addEstablishment(data, successCallback)),
        onUpdateEstablishment: (data) => dispatch(updateEstablishment(data)),
        onAddEstablishmentType: (data, successCallback) => dispatch(addEstablishmentType(data, successCallback)),
    };
};

export default connect(mapStateToProps, mapDispatchToProps)(EstablishmentInfo);