import APIUrl from "../../APIUrl";
import axios from 'axios';

export const GET_RESIDENTS = "GET_RESIDENTS";

function getResidentsAction(residents){return {type: GET_RESIDENTS, residents: residents}};

export const getResidents = function(){
    return function(dispatch){
        return axios.get(APIUrl.getResidents)
            .then(function(response){
                dispatch(getResidentsAction(response.data));
            })
            .catch(function(err){
                throw err;
            });
    };
};

export const checkIfResidentExists = function(resident, existsCallback, noExistsCallback){
    return function(dispatch){
        return axios.post(APIUrl.checkIfResidentExists, resident)
            .then(function(response){
                if(response.status === 200) existsCallback();
                else noExistsCallback();
            });
    };
};

export const addResident = function(data, successCallback){
    return function(dispatch){
        return axios.post(APIUrl.addResident, data)
            .then(function(response){
                dispatch(getResidents());
                successCallback();
            })
            .catch(function(err){
                throw err;
            });
    };
};

export const deleteResident = function(residentId, successCallback){
    return function(dispatch){
        return axios.post(APIUrl.deleteResident + residentId)
            .then(function(response){
                dispatch(getResidents());
                successCallback();
            })
            .catch(function(err){
                throw err;
            });
    };
};