import React from 'react';
import { connect } from 'react-redux';
import { FormattedMessage } from 'react-intl';
import Util from '../../util/Util';
import {addClient, updateClient} from '../../actions/clients/clients';
import {checkIfUsernameExists, checkIfEmailExists} from '../../actions/user/user';

class UserModal extends React.Component {

    constructor(props) {
        super(props);

        if(this.props.user){
            var user = this.props.user;
            this.state = {
                username: user.username,
                name: user.name,
                firstname: user.first_name,
                function: user.function,
                email: user.email,
                phone: user.phone,
                validator: user.validator,
    
                // Errors
                usernameError: null,
                nameError: null,
                firstnameError: null,
                emailError: null,
    
                disabled: false,
            };
        }else{
            this.state = {
                username: "",
                name: "",
                firstname: "",
                function: "",
                email: "",
                phone: "",
                validator: false,
    
                // Errors
                usernameError: null,
                nameError: null,
                firstnameError: null,
                emailError: null,
    
                disabled: false,
            };
        }
        
    }

    scanErrors(noErrorCallback){
        // Check for variables emptyness
        var isUsernameEmpty = Util.emptyString(this.state.username);
        var isNameEmpty = Util.emptyString(this.state.name);
        var isFirstNameEmpty = Util.emptyString(this.state.firstname);
        var isEmailEmpty = Util.emptyString(this.state.email);

        // Empty fields errors
        if(isUsernameEmpty){this.setState({usernameError: <FormattedMessage id="Empty.Username" />});}
        if(isNameEmpty){this.setState({nameError: <FormattedMessage id="Empty.Name" />});}
        if(isFirstNameEmpty){this.setState({firstnameError: <FormattedMessage id="Empty.FirstName" />});}
        if(isEmailEmpty){this.setState({emailError: <FormattedMessage id="Empty.Email" />});}
        
        // Invalid email error 
        if(!Util.isEmail(this.state.email)){this.setState({emailError: <FormattedMessage id="Invalid.Email" />});}

        // We are modifying a user. No need to check for email & username unicity
        if(this.props.user){
            if(!isUsernameEmpty && !isNameEmpty && !isFirstNameEmpty && !isFirstNameEmpty) return noErrorCallback();
            else return;
        }

        // Make sure username is unique
        this.props.onCheckIfUsernameExists(
            this.state.username, 
            () => {this.setState({usernameError: <FormattedMessage id="Username.Already.Exists" />})},
            // Make sure email is unique
            () => this.props.onCheckIfEmailExists(
                this.state.email, 
                () => {this.setState({emailError: <FormattedMessage id="Email.Already.Exists" />})},
                () => {if(!isUsernameEmpty && !isNameEmpty && !isFirstNameEmpty && !isFirstNameEmpty) return noErrorCallback()}
            )
        );
    }

    onComplete(){
        var createUser = () => {
            var user = {
                username: this.state.username,
                name: this.state.name,
                first_name: this.state.firstname,
                function: this.state.function,
                email: this.state.email,
                validator: this.state.validator,
                phone: this.state.phone,
                establishment_id: this.props.establishment._id
            };
    
            this.setState({disabled: true});
    
            this.props.onAddClient(user, () => this.props.close());
        };

        // Validate fields & send to BE
        this.scanErrors(createUser);
    }

    update(field){
        if(!this.props.user) return;

        var updateValue = () => {
            var data = {
                clientId: this.props.user._id,
                updatedField: field,
                updatedValue: this.state[field]
            };
            
            this.props.onUpdateClient(data);
        }

        // Validate fields & send to BE
        this.scanErrors(updateValue);
    }

    onFieldChange(field, value) {
        if(field === "validator") this.setState({ "validator": !this.state.validator });
        else this.setState({ [field]: value });

        // Empty fields errors
        if(field === "name" && !Util.emptyString(value)) this.setState({nameError: null});
        if(field === "firstname" && !Util.emptyString(value)) this.setState({firstnameError: null});

        // Check if username is not empty and not already taken
        if(field === "username" && !Util.emptyString(value)){
            this.setState({usernameError: null});
            this.props.onCheckIfUsernameExists(value, () => {this.setState({usernameError: <FormattedMessage id="Username.Already.Exists" />})}, () => {});
        } 

        // Check if email is not empty and not already taken
        if(field === "email" && !Util.emptyString(value)){
            this.setState({emailError: null});
            this.props.onCheckIfEmailExists(value, () => {this.setState({emailError: <FormattedMessage id="Email.Already.Exists" />})}, () => {});
        }
    }

    stopEvent(e){
        e.stopPropagation();
    }

    render() {
        return (
            <div className="modal-bg show" onClick={() => this.props.close()}>
                <div className="modal fade show">
                    <div className="modal-dialog" role="document" onClick={(e) => this.stopEvent(e)}>
                        <div className="modal-content">
                            <div className="modal-header">
                                <h5 className="modal-title" id="exampleModalLabel">
                                    {(this.props.user) ? <FormattedMessage id="Modify.User" /> : <FormattedMessage id="Add.User" />}
                                </h5>
                                <button type="button" className="close" data-dismiss="modal" aria-label="Close" onClick={() => this.props.close()}>
                                    <span aria-hidden="true">&times;</span>
                                </button>
                            </div>

                            <div className="modal-body">

                                <div className="form-group row">
                                    <label htmlFor="user-username" className="col-12 col-md-5 col-form-label"><FormattedMessage id="Username" /></label>
                                    <div className="col-12 col-md-7">
                                        <input type="text" className="form-control" id="user-username" autoComplete="off"
                                            value={this.state.username}
                                            disabled={this.props.user}
                                            onChange={(e) => this.onFieldChange("username", e.target.value)} />
                                        <div className="text-danger"><small>{this.state.usernameError}</small></div>
                                    </div>
                                </div>

                                <div className="form-group row">
                                    <label htmlFor="user-name" className="col-12 col-md-5 col-form-label"><FormattedMessage id="Name" /></label>
                                    <div className="col-12 col-md-7">
                                        <input type="text" className="form-control" id="user-name" autoComplete="off"
                                            value={this.state.name}
                                            onChange={(e) => this.onFieldChange("name", e.target.value)}
                                            onBlur={(e) => this.update("name")} />
                                        <div className="text-danger"><small>{this.state.nameError}</small></div>
                                    </div>
                                </div>

                                <div className="form-group row">
                                    <label htmlFor="user-firstname" className="col-12 col-md-5 col-form-label"><FormattedMessage id="First.Name" /></label>
                                    <div className="col-12 col-md-7">
                                        <input type="text" className="form-control" id="user-firstname" autoComplete="off"
                                            value={this.state.firstname}
                                            onChange={(e) => this.onFieldChange("firstname", e.target.value)}
                                            onBlur={(e) => this.update("firstname")} />
                                        <div className="text-danger"><small>{this.state.firstnameError}</small></div>
                                    </div>
                                </div>

                                <div className="form-group row">
                                    <label htmlFor="user-function" className="col-12 col-md-5 col-form-label"><FormattedMessage id="Function" /></label>
                                    <div className="col-12 col-md-7">
                                        <input type="text" className="form-control" id="user-function" autoComplete="off"
                                            value={this.state.function}
                                            onChange={(e) => this.onFieldChange("function", e.target.value)}
                                            onBlur={(e) => this.update("function")} />
                                    </div>
                                </div>

                                <div className="form-group row">
                                    <label htmlFor="user-email" className="col-12 col-md-5 col-form-label"><FormattedMessage id="Email" /></label>
                                    <div className="col-12 col-md-7">
                                        <input type="text" className="form-control" id="user-email" autoComplete="off"
                                            value={this.state.email}
                                            disabled={this.props.user}
                                            onChange={(e) => this.onFieldChange("email", e.target.value)} />
                                        <div className="text-danger"><small>{this.state.emailError}</small></div>
                                    </div>
                                </div>

                                <div className="form-group row">
                                    <label htmlFor="user-phone" className="col-12 col-md-5 col-form-label"><FormattedMessage id="Phone" /></label>
                                    <div className="col-12 col-md-7">
                                        <input type="text" className="form-control" id="user-phone" autoComplete="off"
                                            value={this.state.phone}
                                            onChange={(e) => this.onFieldChange("phone", e.target.value)}
                                            onBlur={(e) => this.update("phone")} />
                                    </div>
                                </div>

                                <div className="form-group row">
                                    <label className="col-12 col-md-5 custom-control-label no-after no-before"><FormattedMessage id="Validator" /></label>
                                    <div className="col-12 col-md-7">
                                        <div className="custom-control custom-switch mx-auto switch-success text-left">
                                            <input onChange={(e) => this.onFieldChange("validator", e.target.value)}
                                                type="checkbox"
                                                id="validator-switch"
                                                className="custom-control-input switch-bg-blue"
                                                checked={this.state.validator}
                                                onBlur={(e) => this.update("validator")} />
                                                <label className="custom-control-label" htmlFor="validator-switch"></label>
                                        </div>
                                    </div>
                                </div>

                            </div>

                            {(!this.props.user) && 
                                <div className="modal-footer">
                                    <button type="button" className="btn btn-secondary" data-dismiss="modal" onClick={() => this.props.close()}><FormattedMessage id="Cancel" /></button>
                                    <button type="button" className="btn btn-primary" disabled={this.state.disabled} onClick={() => this.onComplete()}><FormattedMessage id="Add" /></button>
                                </div>
                            }
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}

const mapStateToProps = state => {
    return {
        //
    };
}

const mapDispatchToProps = dispatch => {
    return {
        onAddClient: (data, successCallback) => dispatch(addClient(data, successCallback)),
        onCheckIfUsernameExists: (username, existsCallback, noExistsCallback) => dispatch(checkIfUsernameExists(username, existsCallback, noExistsCallback)),
        onCheckIfEmailExists: (email, existsCallback, noExistsCallback) => dispatch(checkIfEmailExists(email, existsCallback, noExistsCallback)),
        onUpdateClient: (data) => dispatch(updateClient(data))
    };
};

export default connect(mapStateToProps, mapDispatchToProps)(UserModal);