import React from 'react';
import { connect } from 'react-redux';
import {FormattedMessage} from 'react-intl';
import {deleteMaterialType} from "../../../../actions/settings/materialsTypes/materialsTypes";

class MaterialTypeAdminRow extends React.Component {

  deleteMaterialType(materialId){
    var data = {materialId: materialId}
    this.props.onDeleteMaterialType(data)
  }

  render() {
    // 'Delete shipping setting' modal setup
    var modalTitle = <FormattedMessage id="Confirm" />;
    var modalContent = <FormattedMessage id="Material.Type.Remove.Confirmation" />;
    var successCallback = () => this.deleteMaterialType(this.props.materialType._id);
    return (
      <React.Fragment>
        <tr key={this.props.key}>
          <td>{this.props.materialType.name}</td>
          <td className="text-center">
            <i className="fa fa-trash icon-big" onClick={(e) => this.props.openConfModal(modalTitle, modalContent, successCallback)}></i>
          </td>
        </tr>
      </React.Fragment>
    );
  }

}

const mapStateToProps = state => {
  return {
    lang: state.user.lang,
  }
}

const mapDispatchToProps = dispatch => {
  return {
    onDeleteMaterialType: (data) => dispatch(deleteMaterialType(data)),
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(MaterialTypeAdminRow);
