import React from 'react';
import { connect } from 'react-redux';
import { FormattedMessage } from 'react-intl';
import EstablishmentDMModal from './EstablishmentDMModal';
import EstablishmentDMTable from './EstablishmentDMTable';

class EstablishmentDM extends React.Component {

    constructor(props) {
        super(props);

        this.state = {
            modal: null
        };
    }

    openEstablishmentDMModal() {
      this.setState({
        modal: <EstablishmentDMModal
          isOpen={true}
          index={null}
          establishment={this.props.establishment}
          establishmentSettings={this.props.establishmentSettings}
          close={() => this.closeModal()} />
      });
    }

    closeModal() {
      this.setState({ modal: null });
    }

    render() {
        return (
            <React.Fragment>
                <span onClick={(e) => this.openEstablishmentDMModal()} className="iconed-btn nostyle-a hoverable clickable">
                  <span className="mt-3 mb-3">
                    <i className="fa fa-plus-square icon-big text-dark"></i>
                    <p><FormattedMessage id="Add.Material.Fee" /></p>
                  </span>
                </span>

                {(this.props.establishmentSettings &&
                    this.props.establishmentSettings.materials_types &&
                      this.props.establishmentSettings.materials_types.length > 0) &&
                        <EstablishmentDMTable establishmentSettings={this.props.establishmentSettings}  />}

                {this.state.modal}
            </React.Fragment>
        );
    }

}

const mapStateToProps = state => {
    return {
        //
    }
}

const mapDispatchToProps = dispatch => {
    return {
        //
    };
};

export default connect(mapStateToProps, mapDispatchToProps)(EstablishmentDM);
