import React from 'react';
import { connect } from 'react-redux';
import { FormattedMessage, injectIntl } from 'react-intl';
import ClientsModalAdmin from './ClientsModalAdmin';
import {getEstablishmentTypes} from '../../actions/configurables/establishmentTypes';
import { getPaymentMethods } from '../../actions/configurables/paymentMethods';
import { getPaymentDeadlines } from '../../actions/configurables/paymentDeadlines';
import {getEstablishmentSettings} from '../../actions/establishments/establishmentSettings';
import EstablishmentsRow from './EstablishmentsRow';

class Establishments extends React.Component {

    constructor(props) {
        super(props);

        this.state = {
            nameFilter: null,
            addressFilter: null,
            mercurialFilter: null,

            modal: null
        };
    }

    componentWillMount(){
        if(this.props.establishmentTypes.length === 0) this.props.onGetEstablishmentTypes();
        if(this.props.paymentMethods.length === 0) this.props.onGetPaymentMethods();
        if(this.props.paymentDeadlines.length === 0) this.props.onGetPaymentDeadlines();
        if(this.props.establishmentsSettings.length === 0) this.props.onGetEstablishmentSettings();
    }

    openModal(establishment) {
        this.setState({
            modal: <ClientsModalAdmin
                isOpen={true}
                establishment={establishment}
                openModal={(establishment) => this.openModal(establishment)}
                closeModal={() => this.closeModal()}
            />
        });
    }

    closeModal() {
        this.setState({ modal: null });
    }

    getSettings(establishmentId){
        for(let s of this.props.establishmentsSettings){
            if(s.establishment_id === establishmentId) return s;
        }
    }

    getMercurialName(establishmentId){
        var settings = this.getSettings(establishmentId);

        // If no settings, or mercurial wasn't defined yet
        if(!settings || !settings.mercurial_id) return null;

        for(let m of this.props.mercurials){
            if(m._id === settings.mercurial_id) return m.name;
        }
    }

    getClients(establishmentId){
        var clients = [];

        for(let client of this.props.clients){
            if(client.establishment_id === establishmentId){
                clients.push(client);
            }
        }

        return clients;
    };

    render() {
        if (this.props.establishments.length === 0 && this.props.clients.length === 0) {
            return (
                <React.Fragment>
                    <span href="#/" className="iconed-btn hoverable clickable" onClick={(e) => this.openModal()}>
                        <span className="mt-3 mb-3">
                            <i className="fa fa-plus-circle icon-big text-dark"></i>
                            <p><FormattedMessage id="Establishment.Add" /></p>
                        </span>
                    </span>
                    <div className="alert alert-secondary" role="alert">
                        <FormattedMessage id="Empty.Establishment" />
                    </div>

                    {this.state.modal}

                </React.Fragment>
            );
        }

        var establishmentsNodes = this.props.establishments.map((establishment) => {
            if(this.state.nameFilter && this.state.nameFilter !== ""){
                if(establishment.name.toUpperCase().indexOf(this.state.nameFilter.toUpperCase()) === -1) return null;
            }

            if(this.state.addressFilter && this.state.addressFilter !== ""){
                if(establishment.address.toUpperCase().indexOf(this.state.addressFilter.toUpperCase()) === -1) return null;
            }

            var settings = this.getSettings(establishment._id);
            if(this.state.mercurialFilter && this.state.mercurialFilter !== ""){
                if(!settings.mercurial_id || settings.mercurial_id !== this.state.mercurialFilter) return null;
            }

            return (
                <EstablishmentsRow 
                    key={establishment._id} 
                    establishment={establishment}
                    openModal={(establishment) => this.openModal(establishment)}
                    clients={this.getClients(establishment._id)} />
            );
        });

        // Mercurials select choices
        var now = new Date(Date.now());
        var mercurialsNode = this.props.mercurials.map((mercurial) => {
            if(new Date(mercurial.start_date) > now || new Date(mercurial.end_date) < now) return null;
            
            let version = null;
            if(mercurial.version !== 1) version = "(v" + mercurial.version + ")";

            return (<option key={mercurial._id} value={mercurial._id}>{mercurial.name} {version} </option>);
        });

        return (
            <React.Fragment>
                <span href="#/" className="iconed-btn hoverable clickable" onClick={(e) => this.openModal()}>
                    <span className="mt-3 mb-3">
                        <i className="fa fa-plus-circle icon-big text-dark"></i>
                        <p><FormattedMessage id="Establishment.Add" /></p>
                    </span>
                </span>

                <table className="table table-striped-3 table-bordered">
                    <thead>
                        <tr>
                            <th><FormattedMessage id="Establishment" /></th>
                            <th className="d-none d-md-table-cell"><FormattedMessage id="Address" /></th>
                            <th className="d-none d-md-table-cell"><FormattedMessage id="Mercurial" /></th>
                            <th className="d-none d-sm-table-cell"><FormattedMessage id="Total.Cmd.Excl.Tax" /></th>
                            <th className="d-none d-md-table-cell"><FormattedMessage id="Requests" /></th>
                            <th className="d-none d-md-table-cell"><FormattedMessage id="Fleet" /></th>
                            <th className="d-none d-sm-table-cell"><FormattedMessage id="Sales.Rep" /></th>
                            <th><FormattedMessage id="Actions" /></th>
                        </tr>
                    </thead>
                    <tbody>

                        <tr className="d-none d-xl-table-row filter-row">
                            <td>
                                <input className="form-control" type="search" onChange={(e) => {this.setState({nameFilter: e.target.value})}} />
                            </td>
                            <td className="d-none d-md-table-cell">
                                <input className="form-control" type="search" onChange={(e) => {this.setState({addressFilter: e.target.value})}} />
                            </td>
                            <td className="d-none d-md-table-cell">
                                <select id="order-filter-status" className="form-control w-100" onChange={(e) => {this.setState({mercurialFilter: e.target.value})}}>
                                    <option value="">{this.props.intl.formatMessage({id: "All"})}</option>
                                    {mercurialsNode}
                                </select>
                            </td>
                            <td className="d-none d-sm-table-cell"></td>
                            <td className="d-none d-md-table-cell"></td>
                            <td className="d-none d-md-table-cell"></td>
                            <td className="d-none d-sm-table-cell"></td>
                            <td></td>
                        </tr>

                        {establishmentsNodes}

                    </tbody>
                </table>

                {this.state.modal}

            </React.Fragment>
        );
    }

}

const mapStateToProps = state => {
    return {
        clients: state.clients,
        establishments: state.establishments,
        establishmentTypes: state.establishmentTypes,
        paymentMethods: state.paymentMethods,
        paymentDeadlines: state.paymentDeadlines,
        establishmentsSettings: state.establishmentsSettings,
        mercurials: state.mercurials
    }
};

const mapDispatchToProps = dispatch => {
    return {
        onGetEstablishmentTypes: () => dispatch(getEstablishmentTypes()),
        onGetPaymentMethods: () => dispatch(getPaymentMethods()),
        onGetPaymentDeadlines: () => dispatch(getPaymentDeadlines()),
        onGetEstablishmentSettings: () => dispatch(getEstablishmentSettings())
    };
};

export default connect(mapStateToProps, mapDispatchToProps)(injectIntl(Establishments));
