import React from 'react';
import { connect } from 'react-redux';
import CartRow from "./CartRow";
import ConfirmationModal from '../sub/modals/ConfirmationModal';
import PromptModal from '../sub/modals/PromptModal';
import { deleteAll } from '../../actions/cart/cart';
import { FormattedMessage } from 'react-intl';
import { passOrder, passOrderAdmin } from '../../actions/orders/orders';
import ProductsTotalUtil from '../../util/ProductsTotalUtil';
import Roles from "../../enums/Roles";
import CartPromptModalAdmin from "./CartPromptModalAdmin";

class Cart extends React.Component {

    constructor(props) {
        super(props);

        this.state = {
            modal: null,
            disableButton: true
        };
    }

    getProduct(productId) {
        for (let product of this.props.products) {
            if (product._id === productId) return product;
        }

        return null;
    }

    passOrder(e) {
        var comment = document.getElementById("comment").value;
        var data = {comment: comment};

        // Admin
        if(this.props.user.role === Roles.ADMIN){
            // Add the clientId to the call
            var clientId = document.getElementById("order-clientId").value;
            data.clientId = clientId;

            this.props.onPassOrderAdmin(data);
        }
        // User
        else{
            this.props.onPassOrder(data);
        }
    }

    deleteAll(e) {
        this.props.onDeleteAll();
    }

    openConfModal(title, content, successCallback) {
        this.setState({
            modal: <ConfirmationModal isOpen={true}
                title={title}
                content={content}
                successCallback={successCallback}
                closeModal={() => this.closeModal()} />
        });
    }

    openOrderPromptModal() {
        // Admin user
        if (this.props.user.role === Roles.ADMIN) {
            this.setState({
                modal: <CartPromptModalAdmin
                    isOpen={true}
                    successCallback={(e) => this.passOrder()}
                    closeModal={() => this.closeModal()} />
            });
        }
        // Normal user
        else {
            var promptModalTitle = <FormattedMessage id="Order.Confirm" />;
            var promptModalContent = <FormattedMessage id="Comment" />;

            this.setState({
                modal: <PromptModal isOpen={true}
                    title={promptModalTitle}
                    content={promptModalContent}
                    successCallback={(e) => this.passOrder()}
                    closeModal={() => this.closeModal()} />
            });
        }
    }

    closeModal() {
        this.setState({ modal: null });
    }

    render() {
        var ordersSettings;
        if(this.props.ordersSettings.length === 0 && this.props.ordersSettingsAdmin.length === 0) return null;
        if(this.props.ordersSettings.length !== 0 ) ordersSettings = this.props.ordersSettings;
        if(this.props.ordersSettingsAdmin.length !== 0 ) ordersSettings = this.props.ordersSettingsAdmin;

        if (this.props.cart.length === 0 || this.props.products.length === 0) {
            return (
                <React.Fragment>
                    <a href="#/home/families" className="iconed-btn nostyle-a hoverable">
                        <span className="mt-3 mb-3">
                            <i className="fa fa-arrow-circle-left icon-big text-dark"></i>
                            <p><FormattedMessage id="Categories" /></p>
                        </span>
                    </a>
                    <div className="alert alert-secondary" role="alert">
                        <FormattedMessage id="Empty.Cart" />
                    </div>
                </React.Fragment>
            );
        }

        // Helper that will do all calculations for us
        var productsTotalUtil = new ProductsTotalUtil(this.props.cart, this.props.products, null, null, ordersSettings);

        // 'Delete cart' modal setup
        var modalTitle = <FormattedMessage id="Confirm" />;
        var modalContent = <FormattedMessage id="Empty.Cart.Confirm" />;
        var successCallbackConfModal = (e) => this.deleteAll(e);

        let cartNode = this.props.cart.map((cart) => {
            var product = this.getProduct(cart.id_product);

            return (
                <CartRow
                    key={cart._id}
                    cart={cart}
                    product={product}
                    openConfModal={(title, content, successCallback) => this.openConfModal(title, content, successCallback)} />
            );
        });

        var shoppingAlert = null;
        var shippingAlert = null;

        if (!productsTotalUtil.aboveMinToOrder()) {
            shoppingAlert = <div className='alert alert-danger' role='alert'><FormattedMessage id="Min.Order.Cost" values={{ cost: productsTotalUtil.min_cost_to_order }} /></div>;
        }

        if (productsTotalUtil.freeShipping()) {
            var max = ordersSettings.shipping_costs[ordersSettings.shipping_costs.length - 1].max;
            var min = ordersSettings.shipping_costs[ordersSettings.shipping_costs.length - 1].min
            shippingAlert = <div className='alert alert-info' role='alert'><FormattedMessage id="Min.Shipping.Cost" values={{cost: max || min }}/></div>;
        }

        let buttonsClassName = "btn-success";
        if (this.props.user.role === Roles.ADMIN) buttonsClassName = "btn-primary";

        return (
            <React.Fragment>
                <a href="#/home/families" className="iconed-btn nostyle-a hoverable">
                    <span className="mt-3">
                        <i className="fa fa-arrow-circle-left icon-big text-dark"></i>
                        <p><FormattedMessage id="Categories" /></p>
                    </span>
                </a>

                <div className="content">

                    {this.state.modal}
                    {shoppingAlert}
                    {shippingAlert}

                    <table className="table table-striped table-bordered">
                        <thead>
                            <tr>
                                <th className="d-none d-lg-table-cell"><FormattedMessage id="Category" /></th>
                                <th><FormattedMessage id="Designation" /></th>
                                <th className="d-none d-md-table-cell"><FormattedMessage id="Vending.unit" /></th>
                                <th className="d-none d-lg-table-cell"><FormattedMessage id="Reference" /></th>
                                <th><FormattedMessage id="Qty" /></th>
                                <th className="d-none d-lg-table-cell"><FormattedMessage id="Unit.Price.Excl.Tax" /></th>
                                <th className="d-none d-md-table-cell"><FormattedMessage id="Unit.Price.Incl.Tax" /></th>
                                <th className="d-none d-lg-table-cell"><FormattedMessage id="Total" /> <FormattedMessage id="Excl.Tax" /></th>
                                <th><FormattedMessage id="Total" /> <FormattedMessage id="Incl.Tax" /></th>
                                <th className="d-none d-md-table-cell"><FormattedMessage id="Urgent" /></th>
                                <th><FormattedMessage id="Actions" /></th>
                            </tr>
                        </thead>
                        <tbody>
                            {cartNode}
                        </tbody>
                    </table>

                    <div className="card col-12 col-lg-6 offset-lg-6">
                        <div className="card-body">
                            <table className="table">
                                <tbody>
                                    {(productsTotalUtil.aboveMinToOrder()) &&
                                        <tr>
                                            <td><FormattedMessage id="Shipping.Cost" />&nbsp;:</td>
                                            <td><b>{productsTotalUtil.shippingCost}</b></td>
                                        </tr>
                                    }
                                    {(productsTotalUtil.urgent) &&
                                        <tr>
                                            <td><FormattedMessage id="Emergency.Cost" /></td>
                                            <td><b>{productsTotalUtil.urgent_shipping_cost}</b></td>
                                        </tr>
                                    }
                                    <tr>
                                        <td><FormattedMessage id="Total.Excl.Tax" /></td>
                                        <td><b>{productsTotalUtil.totalHt}</b></td>
                                    </tr>
                                    <tr>
                                        <td><FormattedMessage id="VAT" /></td>
                                        <td><b>{productsTotalUtil.totalTva}</b></td>
                                    </tr>
                                    <tr>
                                        <td><FormattedMessage id="Total.Incl.Tax" /></td>
                                        <td><b>{productsTotalUtil.totalTtc}</b></td>
                                    </tr>
                                </tbody>
                            </table>

                            <div className="container">
                                <div className="row text-center">
                                    <div className="col-12 col-sm-6 mb-1">
                                        <button className={"btn btn-m " + buttonsClassName + " btn-block"} onClick={(e) => this.openConfModal(modalTitle, modalContent, successCallbackConfModal)}><FormattedMessage id="Cart.Empty" /></button>
                                    </div>
                                    <div className="col-12 col-sm-6">
                                        <button className={"btn btn-m " + buttonsClassName + " btn-block"} onClick={(e) => this.openOrderPromptModal()} disabled={!productsTotalUtil.aboveMinToOrder()}><FormattedMessage id="Order.Pass" /></button>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>

                </div>

            </React.Fragment>
        );
    }
}

const mapStateToProps = state => {
    return {
        lang: state.i18n.lang,
        cart: state.cart,
        products: state.products,
        user: state.user,
        ordersSettings: state.ordersSettings,
        ordersSettingsAdmin: state.ordersSettingsAdmin
    }
}

const mapDispatchToProps = dispatch => {
    return {
        onDeleteAll: () => dispatch(deleteAll()),
        onPassOrder: (data) => dispatch(passOrder(data)),
        onPassOrderAdmin: (data) => dispatch(passOrderAdmin(data)),
    };
};

export default connect(mapStateToProps, mapDispatchToProps)(Cart);
