import React from 'react';
import { connect } from 'react-redux';
import { FormattedMessage } from 'react-intl';
import OrderProductsAdmin from "./OrderProductsAdmin";
import { splitOrderAdmin } from "../../../actions/orders/orders";


import "../../../css/cart/cart.css";

class SplitOrderModalAdmin extends React.Component {

    constructor(props) {
        super(props);

        this.state = {
            disabled: true,
        };
    }

    close() {
        this.props.closeConfModal();
    }

    getSplittedProducts() {
        var productsAvailable = [];
        var productsUnavailable = [];

        var inputs = document.getElementsByClassName("disponibility");
        for (var product of this.props.order.products) {
            for (var input of inputs) {
                if (input.checked && input.value === product.id_product) {
                    productsAvailable.push(product);
                }
                if (!input.checked && input.value === product.id_product) {
                    productsUnavailable.push(product);
                }
            }
        }

        return {
            productsAvailable: productsAvailable,
            productsUnavailable: productsUnavailable
        };
    }

    checkProductsDisponibility() {
        var data = this.getSplittedProducts();
        this.setState({ disabled: (data.productsAvailable.length === 0 || data.productsUnavailable.length === 0) });
    }

    split(e, order) {
        var data = this.getSplittedProducts();
        data.orderId = order._id;

        this.props.onSplitOrderAdmin(data);
        this.close();
    }

    stopEvent(e) {
        e.preventDefault();
        e.stopPropagation();
    }

    render() {
        return (
            <div className="modal-bg show" onClick={() => this.close()}>
                <div className="modal fade show">
                    <div className="modal-dialog modal-lg" role="document" onClick={(e) => this.stopEvent(e)}>
                        <div className="modal-content">
                            <div className="modal-header">
                                <h5 className="modal-title" id="exampleModalLabel"><span> <FormattedMessage id="Order" /> {this.props.order.ref} </span></h5>
                                <button type="button" className="close" data-dismiss="modal" aria-label="Close" onClick={() => this.close()}>
                                    <span aria-hidden="true">&times;</span>
                                </button>
                            </div>
                            <div className="modal-body">
                                <div className="row text-center">
                                    <div className="col-12 col-sm-6 p-3">
                                        <span className="d-inline-block mr-3"><FormattedMessage id="Product.Available" /></span>
                                        <div className="custom-control custom-switch mx-auto d-inline-block">
                                            <input onChange={(e) => this.stopEvent(e)}
                                                type="checkbox"
                                                className={"custom-control-input switch-bg-blue"}
                                                id={"switch-av"}
                                                checked={true} />
                                            <label className="custom-control-label cursor-pointer" htmlFor={"switch-av"}></label>
                                        </div>
                                    </div>
                                    <div className="col-12 col-sm-6 p-3">
                                        <span className="d-inline-block mr-3"><FormattedMessage id="Product.Unavailable" /></span>
                                        <div className="custom-control custom-switch mx-auto d-inline-block">
                                            <input onChange={(e) => this.stopEvent(e)}
                                                type="checkbox"
                                                className={"custom-control-input switch-bg-blue"}
                                                id={"switch-av-f"}
                                                checked={false} />
                                            <label className="custom-control-label cursor-pointer" htmlFor={"switch-av-f"}></label>
                                        </div>
                                    </div>
                                </div>
                                <OrderProductsAdmin order={this.props.order} onClickCallback={() => this.checkProductsDisponibility()} />
                            </div>
                            <div className="modal-footer">
                                <button type="button" className="btn btn-secondary" data-dismiss="modal" onClick={() => this.close()}><FormattedMessage id="Cancel" /></button>
                                <button type="button" className="btn btn-primary" onClick={(e) => this.split(e, this.props.order)} disabled={this.state.disabled}><FormattedMessage id="Split" /></button>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}

const mapStateToProps = state => {
    return {
        lang: state.i18n.lang,
    }
}

const mapDispatchToProps = dispatch => {
    return {
        onSplitOrderAdmin: (data) => dispatch(splitOrderAdmin(data))
    };
};

export default connect(mapStateToProps, mapDispatchToProps)(SplitOrderModalAdmin);
