import React from 'react';
import { connect } from 'react-redux';
import FileDropZone from '../sub/FileDropZone';

import {FormattedMessage} from 'react-intl';
import Util from '../../util/Util';
import FileUtil from '../../util/FileUtil';
import APIUrl from '../../APIUrl';
import Axios from 'axios';
import ConfirmationModal from '../sub/modals/ConfirmationModal';

class MercurialImgsModal extends React.Component {

    constructor(props){
        super(props);

        this.state = {
            modal: null,
            disabled: false,
            // Uploaded files
            summary: null,
            productFiles: null,
            familyFiles: null,
            declinedFiles: null,
            // Info
            familiesWithNoImg: null,
            productsWithNoImg: null,
        };
    }

    componentWillMount(){
        if(!this.state.summary){
            Axios.get(APIUrl.getMercImgOverview + this.props.mercurial._id)
                .then((response) => {
                    this.setState({
                        familiesWithNoImg: response.data.familiesWithNoImg,
                        productsWithNoImg: response.data.productsWithNoImg,
                    });
                });
        }
    }

    close() {
        this.props.closeModal();
    }

    stopEvent(e) {
        e.stopPropagation();
    }

    onComplete(){
        this.setState({disabled: true});

        this.props.onComplete(this.props.mercurial._id, this.state.productFiles, this.state.familyFiles);
    }

    onDropFiles(files){
        let productFiles = [];
        let familyFiles = [];
        let declinedFiles = [];

        let products = this.getMercurialProducts(this.props.mercurial);

        outer:for(let file of files){

            for(let product of products){
                let fileNameWithoutExtension = FileUtil.withoutExtension(file.name);

                // Product img
                if(fileNameWithoutExtension === product.ref){
                    productFiles.push(file);
                    continue outer;
                }
                // Family img
                else if(fileNameWithoutExtension === product.fam.split(".")[0]){
                    familyFiles.push(file);
                    continue outer;
                }
            }

            declinedFiles.push(file);
        }

        this.setState({
            productFiles: productFiles,
            familyFiles: familyFiles,
            declinedFiles: declinedFiles
        });
    }

    disabled(){
        return this.state.disabled || (Util.emptyArray(this.state.productFiles) && Util.emptyArray(this.state.familyFiles));
    }

    openMissingModal(families, products) {
        var title = <FormattedMessage id="Mercurials.Missing.Items" />;

        var familiesContent = null;
        if(families.length > 0){
            familiesContent = (
                <div>
                    <div><FormattedMessage id="Mercurials.Missing.Families" /> :</div>
                    <ul>
                        {families.map(family => <li>{family.fam.split(".")[0]} : {family.famille}</li>)}
                    </ul>
                </div>
            )
        }

        var productsContent = null;
        if(products.length > 0){
            productsContent = (
                <div>
                    <div><FormattedMessage id="Mercurials.Missing.Products" /> :</div>
                    <ul>
                        {products.map(product => {
                            let productDes = product.designation;
                            if(productDes[productDes.length-1] === "-") productDes = productDes.slice(0, -1);
                            
                            return <li><span className="text-muted">({product.famille})</span> {product.ref} : {productDes}</li>}
                        )}
                    </ul>
                </div>
            )
        }

        var content = <span>{familiesContent}{productsContent}</span>

        this.setState({
            modal: <ConfirmationModal isOpen={true}
                size={"xl"}
                title={title}
                content={content}
                successCallback={() => this.closeModal()}
                closeModal={() => this.closeModal()} />
        });
    }

    closeModal(){
        this.setState({modal: null});
    }

    displayMissingImgs(){
        // Users has dropped stuff, don't show
        if(this.state.productFiles || this.state.familyFiles || this.state.declinedFiles) return false;

        return this.state.familiesWithNoImg && 
            this.state.productsWithNoImg && 
            (this.state.familiesWithNoImg.length !== 0 || this.state.productsWithNoImg.length !== 0);
    }
    
    getMercurialProducts(mercurial){
        let products = [];

        for(let product of this.props.products){
            if(product.mercurial_id === mercurial._id){
                products.push(product);
            }
        }

        return products;
    }

    render() {
        // Alert box with info about how much missing imgs this mercurial has, with the list of the items
        var missingImgs = null;
        if(this.displayMissingImgs()){
            var missingFamilyImgs = null;
            if(this.state.familiesWithNoImg.length !== 0){
                missingFamilyImgs = (<li>{this.state.familiesWithNoImg.length} <FormattedMessage id="Mercurials.Missing.Family.Images" /></li>);
            } 
            
            var missingProductImgs = null;
            if(this.state.productsWithNoImg.length !== 0){
                missingProductImgs = (<li>{this.state.productsWithNoImg.length} <FormattedMessage id="Mercurials.Missing.Product.Images" /></li>);
            }

            missingImgs = (
                <div className="alert alert-danger hoverable clickable" onClick={() => this.openMissingModal(this.state.familiesWithNoImg, this.state.productsWithNoImg)} role="alert">
                    <FormattedMessage id="Mercurials.Missing" /> :
                    <ul className="pb-0 mb-0">
                        {missingFamilyImgs}
                        {missingProductImgs}
                    </ul>
                </div>
            );
        }

        return (
            <React.Fragment>

                <div className="modal-bg show" onClick={() => this.close()}>
                    <div className="modal fade show">
                        <div className="modal-dialog modal-lg" role="document" onClick={(e) => this.stopEvent(e)}>

                            <div className="modal-content">

                                <div className="modal-header">
                                    <h5 className="modal-title" id="exampleModalLabel"><FormattedMessage id="Mercurials.Import.Imgs" /></h5>
                                    
                                    <button type="button" className="close" data-dismiss="modal" aria-label="Close" onClick={() => this.close()}>
                                        <span aria-hidden="true">&times;</span>
                                    </button>
                                </div>

                                <div className="modal-body p-4">
                                    <h4 className="w-100 text-center "><FormattedMessage id="Mercurials.Upload.Imgs" /></h4>
                                    <p className="mb-3 w-100 text-center font-weight-light"><FormattedMessage id="Mercurials.Upload.Imgs.Rules" /></p>
                                    <p className="mb-3 w-100 text-center font-weight-light text-success"><FormattedMessage id="Mercurials.Upload.Imgs.Recommended.Dimensions" /></p>

                                    {missingImgs}

                                    <FileDropZone 
                                        onDropFiles={(files) => this.onDropFiles(files)} 
                                        acceptedExtensions={["jpg", "png"]}
                                        multiple={true}
                                        />

                                    <div className="text-center pt-3">
                                        <span className="text-success">{(this.state.familyFiles) && <span> {this.state.familyFiles.length} <FormattedMessage id="Mercurials.Missing.Family.Images" /> / </span>}</span>
                                        <span className="text-success">{(this.state.productFiles) && <span> {this.state.productFiles.length} <FormattedMessage id="Mercurials.Missing.Product.Images" /> / </span>}</span>
                                        <span className="text-danger">{(this.state.declinedFiles) && <span> {this.state.declinedFiles.length} <FormattedMessage id="Mercurials.Invlid.Images" /></span>}</span>
                                    </div>
                                </div>

                                <div className="modal-footer">
                                    <button type="button" className="btn btn-secondary" data-dismiss="modal" onClick={() => this.close()}><FormattedMessage id="Cancel" /></button>
                                    <button type="button" className="btn btn-primary" 
                                        disabled={this.disabled()}
                                        onClick={() => this.onComplete()}><FormattedMessage id="Import" /></button>
                                </div>

                            </div>
                        </div>
                    </div>
                </div>
                
                {this.state.modal}
            </React.Fragment>
        );
    }
}

const mapStateToProps = state => {
    return {
        mercurials: state.mercurials,
        products: state.products
    }
}

const mapDispatchToProps = dispatch => {
    return {
        //
    };
};

export default connect(mapStateToProps, mapDispatchToProps)(MercurialImgsModal);

