import APIUrl from "../../APIUrl";
import axios from 'axios';

export const GET_USER = "GET_USER";

function getUserAction(user){return {type: GET_USER, user: user}};

export const getUser = function(){
    return function(dispatch){
        return axios.get(APIUrl.getUser)
            .then(function(response){
                if (!response || !response.status) window.location.href = "/";
                
                dispatch(getUserAction(response.data));
            })
            .catch(function(err){
                window.location.href = "/";
            });
    };
};

export const updateLang = function(lang){
    return function(dispatch){
        return axios.get(APIUrl.updateLang + lang)
            .then(function(response){
                // Refresh the user state
                dispatch(getUser());
            })
            .catch(function(err){
                throw err;
            });
    };
};

export const checkIfUsernameExists = function(username, existsCallback, noExistsCallback){
    return function(dispatch){
        return axios.get(APIUrl.checkIfUsernameExists + username)
            .then(function(response){
                if(response.status === 200) existsCallback();
                else noExistsCallback();
            });
    };
};

export const checkIfEmailExists = function(email, existsCallback, noExistsCallback){
    return function(dispatch){
        return axios.get(APIUrl.checkIfEmailExists + email)
            .then(function(response){
                if(response.status === 200) existsCallback();
                else noExistsCallback();
            });
    };
};