import React from 'react';
import { connect } from 'react-redux';
import { FormattedMessage } from 'react-intl';
import UserModal from './UserModal';
import Users from './Users';

class EstablishmentUsers extends React.Component {

    constructor(props) {
        super(props);

        this.state = {
            modal: null
        };
    }

    closeModal() {
        this.setState({ modal: null });
    }

    openModal(){
        this.setState({
            modal: (
                <UserModal 
                    establishment={this.props.establishment}
                    close={() => this.closeModal()} />
            )
        });
    }

    render() {
        if (this.props.clients.length === 0) {
            return (
                <React.Fragment>
                    <span href="#/" className="iconed-btn hoverable clickable" onClick={(e) => this.openModal()}>
                        <span className="mb-3">
                            <i className="fa fa-plus-circle icon-big text-dark"></i>
                            <p><FormattedMessage id="User.Add" /></p>
                        </span>
                    </span>
                    <div className="alert alert-secondary" role="alert">
                        <FormattedMessage id="Empty.Users" />
                    </div>

                    {this.state.modal}

                </React.Fragment>
            );
        };

        return (
            <React.Fragment>
                <span href="#/" className="iconed-btn hoverable clickable" onClick={(e) => this.openModal()}>
                    <span className="mb-3">
                        <i className="fa fa-plus-circle icon-big text-dark"></i>
                        <p><FormattedMessage id="User.Add" /></p>
                    </span>
                </span>

                <Users clients={this.props.clients} />

                {this.state.modal}

            </React.Fragment>
        );
    }

}

const mapStateToProps = state => {
    return {
        //
    }
}

const mapDispatchToProps = dispatch => {
    return {
        //
    };
};

export default connect(mapStateToProps, mapDispatchToProps)(EstablishmentUsers);
