import React from 'react';
import {connect} from 'react-redux';
import {login} from '../../actions/authentication/authentication';
import {FormattedMessage} from 'react-intl';

class Login extends React.Component {

    handleSubmit(e) {
        e.preventDefault();
        e.stopPropagation();

        let that = this;
        function onLoginSuccess(){
            that.props.history.push('/home');
        };

        function showErrorMsg(){
            var errorMsg = document.getElementById("loginFail");
            errorMsg.classList.remove("d-none");
            errorMsg.classList.add("d-block");
        };

        function hideErrorMsg(){
            var errorMsg = document.getElementById("loginFail");
            errorMsg.classList.remove("d-block");
            errorMsg.classList.add("d-none");
        };

        hideErrorMsg();

        // Send login information and redirect on success
        this.props.onLogin(e.target.username.value, e.target.password.value, onLoginSuccess, showErrorMsg);
    };

    render() {
        return (
            <div className="jumbotron vertical-center no-bg p-0 m-0 bg-light-grey">

                <div className="container p-5 mw-600 border border-dark bg-light">
                    <a href="/#" className="nostyle-a">
                        <h3 className="text-center text-success mb-3">E4COLL</h3>
                    </a>

                    <form onSubmit={(e) => this.handleSubmit(e)}>

                        <div className="form-group">
                            <label><FormattedMessage id="Username" /></label>
                            <input type="text" id="username" name="username" className="form-control" />
                        </div>

                        <div className="form-group">
                            <label><FormattedMessage id="Password" /></label>
                            <input type="password" className="form-control" id="pwd" name="password" />
                        </div>

                        <small id="loginFail" className="form-text text-danger mb-3 d-none"><FormattedMessage id="Login.Fail" /></small>
                        
                        <button type="submit" className="btn btn-success btn-block"><FormattedMessage id="Connect" /></button>
                    </form>

                </div>

            </div>
        );
    }
}

const mapStateToProps = state => {
    return {
        lang: state.i18n.lang
    }
  }

const mapDispatchToProps = dispatch => {
    return {
        onLogin: (username, password, successCallback, failureCallback) => dispatch(login(username, password, successCallback, failureCallback))
    };
};

export default connect(mapStateToProps, mapDispatchToProps)(Login);