import React from 'react';
import { connect } from 'react-redux';
import PriceUtil from "../../util/PriceUtil";
import { FormattedMessage } from 'react-intl';
import OrderProductRow from "./OrderProductRow";
import Roles from "../../enums/Roles";

class OrderProducts extends React.Component {

    getProduct(productId) {
        // Product from regular mercurial
        for (let product of this.props.products) {
            if (product._id === productId) return product;
        }

        // Product from outdated mercurial
        for(let oldProduct of this.props.oldProducts){
            if (oldProduct._id === productId) return oldProduct;
        }
    }

    render() {
        if (this.props.products.length === 0 && this.props.oldProducts.length === 0) return null;

        // Helper class for prices calculations
        var priceUtil = new PriceUtil();

        let key = 0;
        let productNode = this.props.order.products.map((p) => {
            var product = this.getProduct(p.id_product);
            priceUtil.add(product, p.quantity);

            return (
                <OrderProductRow
                    key={++key + "-" + product._id}
                    product={product}
                    quantity={p.quantity}
                    urgent={p.urgent}
                    />
            );
        });

        let commentClassName = (this.props.user.role === Roles.ADMIN) ? "alert-info" : "alert-success";

        let commentNode = null;
        if(this.props.order.comment && this.props.order.comment !== ""){
            commentNode = (
                <tr>
                    <td colSpan="1000">
                        <div className={"alert " + commentClassName + " mb-0 break-word"} role="alert">
                            <b><FormattedMessage id="Comment" /></b> : {this.props.order.comment}
                        </div>
                    </td>
                </tr>
            );
        }

        return (
            <table className="table table-striped table-bordered m-0">
                <thead>
                    <tr>
                        <th className="d-none d-md-table-cell"><FormattedMessage id="Reference" /></th>
                        <th><FormattedMessage id="Designation" /></th>
                        <th><FormattedMessage id="Quantity" /></th>
                        <th className="d-none d-md-table-cell"><FormattedMessage id="Unit.Price.Excl.Tax" /></th>
                        <th><FormattedMessage id="Total.Excl.Tax" /></th>
                    </tr>
                </thead>
                <tbody>
                    {productNode}
                    {commentNode}
                </tbody>
            </table>
        );
    }

}

const mapStateToProps = state => {
    return {
        products: state.products,
        oldProducts: state.oldProducts,
        lang: state.i18n.lang,
        user: state.user
    }
}

const mapDispatchToProps = dispatch => {
    return {
        //
    };
};

export default connect(mapStateToProps, mapDispatchToProps)(OrderProducts);
