import React from 'react';
import { connect } from 'react-redux';
import { FormattedMessage } from 'react-intl';
import MaterialsTypesModalAdmin from './MaterialsTypesModalAdmin';
import MaterialsTypesTableAdmin from './MaterialsTypesAdminTable';

class MaterialsTypesAdmin extends React.Component {

  constructor(props) {
    super(props);

    this.state = {
      modal: null,
    };
  }

  openMaterialsTypesModal() {
    this.setState({
      modal: <MaterialsTypesModalAdmin
        isOpen={true}
        closeModal={() => this.closeModal()} />
    });
  }

  closeModal() {
    this.setState({ modal: null });
  }

  render() {
    return (
      <React.Fragment>

        <span onClick={(e) => this.openMaterialsTypesModal()} className="iconed-btn nostyle-a hoverable clickable">
          <span className="mt-3 mb-3">
            <i className="fa fa-plus-square icon-big text-dark"></i>
            <p><FormattedMessage id="Add.Material" /></p>
          </span>
        </span>

        {(this.props.materialsTypes.length !== 0) && <MaterialsTypesTableAdmin materialsTypes={this.props.materialsTypes} />}

        {this.state.modal}

      </React.Fragment>
    );
  }

}

const mapStateToProps = state => {
  return {
    user: state.user,
    materialsTypes: state.materialsTypes,
  }
}

const mapDispatchToProps = dispatch => {
  return {
    //
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(MaterialsTypesAdmin);
