export default class DateUtil{

    static toDate(date){
        date = new Date(date);
        let dd = date.getDate();
        let MM = date.getMonth()+1; 
        const yyyy = date.getFullYear();

        if(dd < 10) dd = `0${dd}`;
        if(MM < 10)  MM = `0${MM}`;

        return `${dd}/${MM}/${yyyy}`;
    }

    static toDateWithHour(date){
        date = new Date(date);
        let dd = date.getDate();
        let MM = date.getMonth()+1; 
        const yyyy = date.getFullYear();

        if(dd < 10) dd = `0${dd}`;
        if(MM < 10)  MM = `0${MM}`;

        let hh = date.getHours();
        let mm = date.getMinutes();

        if(mm < 10) mm = `0${mm}`;

        return `${dd}/${MM}/${yyyy} ${hh}:${mm}`;
    }

}