import React from 'react';
import { connect } from 'react-redux';
import { FormattedMessage } from 'react-intl';
import Util from '../../util/Util';
import { updateEstablishmentSettings } from '../../actions/establishments/establishmentSettings';

class EstablishmentDMModal extends React.Component {

    constructor(props) {
        super(props);

        this.state = {
            material_type_id: "",
            daily_fee: 0,
            long_term_fee: 0,
            maintenance_fee: 0
        };

        // Update
        if (this.props.index !== null) {
            var materialType = this.props.establishmentSettings.materials_types[this.props.index];

            this.state = {
                material_type_id: materialType.material_type_id,
                daily_fee: materialType.daily_fee,
                long_term_fee: materialType.long_term_fee,
                maintenance_fee: materialType.maintenance_fee
            };
        }
    }

    close() {
        this.props.close();
    }

    stopEvent(e) {
        e.preventDefault();
        e.stopPropagation();
    }

    checkMaterialsTypes(materialTypeId) {
        if (this.props.establishmentSettings.materials_types) {
            for (var mT of this.props.establishmentSettings.materials_types) {
                if (mT.material_type_id === materialTypeId) return true;
            }
        }
    }

    add() {
        if (this.hasEmptyField()) return;

        let materialsTypes = this.props.establishmentSettings.materials_types || [];

        var materialType = {
            material_type_id: this.state.material_type_id,
            daily_fee: parseInt(this.state.daily_fee),
            long_term_fee: parseInt(this.state.long_term_fee),
            maintenance_fee: parseInt(this.state.maintenance_fee)
        };

        materialsTypes.push(materialType);

        var data = {
            establishmentId: this.props.establishment._id,
            updatedField: "materials_types",
            updatedValue: materialsTypes,
        };

        this.props.onUpdateEstablishmentSettings(data, () => this.close());
    }

    update() {
        if(this.props.index === undefined || this.props.index === null) return;

        if (this.hasEmptyField()) return;

        let materialsTypes = this.props.establishmentSettings.materials_types;

        materialsTypes[this.props.index]["material_type_id"] = this.state.material_type_id;
        materialsTypes[this.props.index]["daily_fee"] = parseInt(this.state.daily_fee);
        materialsTypes[this.props.index]["long_term_fee"] = parseInt(this.state.long_term_fee);
        materialsTypes[this.props.index]["maintenance_fee"] = parseInt(this.state.maintenance_fee);

        var data = {
            establishmentId: this.props.establishmentSettings.establishment_id,
            updatedField: "materials_types",
            updatedValue: materialsTypes,
        };

        this.props.onUpdateEstablishmentSettings(data, () => this.close());
    }

    hasEmptyField() {
        return (Util.emptyString(this.state.material_type_id) ||
            Util.emptyString(this.state.daily_fee) ||
            Util.emptyString(this.state.long_term_fee) ||
            Util.emptyString(this.state.maintenance_fee));

    }

    onChange(key, value) {
        this.setState({ [key]: value });
    }

    render() {
        let typeSelectOptions = null;

        if (this.props.materialsTypes.length > 0) {
            typeSelectOptions = this.props.materialsTypes.map((materialType) => {
                var disabled = this.checkMaterialsTypes(materialType._id);
                return (<option key={materialType._id} value={materialType._id} disabled={disabled}>{materialType.name}</option>);
            });
        }

        var title = <FormattedMessage id="Add.Material.Fee" />;
        if(this.props.index !== undefined && this.props.index !== null) title = <FormattedMessage id="Modify.Material.Fee" />;

        return (
            <div className="modal-bg show" onClick={() => this.props.close()}>
                <div className="modal fade show">
                    <div className="modal-dialog" role="document" onClick={(e) => this.stopEvent(e)}>
                        <div className="modal-content">
                            <div className="modal-header">
                                <h5 className="modal-title" id="exampleModalLabel">{title}</h5>
                                <button type="button" className="close" data-dismiss="modal" aria-label="Close" onClick={() => this.props.close()}>
                                    <span aria-hidden="true">&times;</span>
                                </button>
                            </div>

                            <div className="modal-body">

                                <div className="form-group row">
                                    <label htmlFor="materialType" className="col-12 col-md-5 col-form-label"><FormattedMessage id="Type" /></label>
                                    <div className="col-12 col-md-7">
                                        <select id="materialType" className="form-control" value={this.state.material_type_id}
                                                onBlur={() => this.update()}
                                                onChange={(e) => this.onChange("material_type_id", e.target.value)}>
                                                <option value=""></option>
                                                {typeSelectOptions}
                                            </select>
                                    </div>
                                </div>

                                <div className="form-group row">
                                    <label htmlFor="dailyFee" className="col-12 col-md-5 col-form-label"><FormattedMessage id="Daily.Fee" /></label>
                                    <div className="col-12 col-md-7">
                                        <input type="number" className="form-control" min="0" id="dailyFee" value={this.state.daily_fee}
                                            onBlur={() => this.update()}
                                            onChange={(e) => this.onChange("daily_fee", e.target.value)} />
                                    </div>
                                </div>

                                <div className="form-group row">
                                    <label htmlFor="longTermFee" className="col-12 col-md-5 col-form-label"><FormattedMessage id="Long.Term.Fee" /></label>
                                    <div className="col-12 col-md-7">
                                        <input type="number" className="form-control" min="0" id="longTermFee" value={this.state.long_term_fee}
                                            onBlur={() => this.update()}
                                            onChange={(e) => this.onChange("long_term_fee", e.target.value)} />
                                    </div>
                                </div>

                                <div className="form-group row">
                                    <label htmlFor="maintenanceFee" className="col-12 col-md-5 col-form-label"><FormattedMessage id="Maintenance.Fee" /></label>
                                    <div className="col-12 col-md-7">
                                    <input type="number" className="form-control" min="0" id="maintenanceFee" value={this.state.maintenance_fee}
                                            onBlur={() => this.update()}
                                            onChange={(e) => this.onChange("maintenance_fee", e.target.value)} />
                                    </div>
                                </div>

                            </div>

                            {(this.props.index === undefined || this.props.index === null) &&
                                <div className="modal-footer">
                                    <button type="button" className="btn btn-secondary" data-dismiss="modal" onClick={() => this.props.close()}><FormattedMessage id="Cancel" /></button>
                                    <button type="button" className="btn btn-primary" onClick={() => this.add()} disabled={this.hasEmptyField()}><FormattedMessage id="Add" /></button>
                                </div>
                            }

                        </div>
                    </div>
                </div>
            </div>
        );
    }
}

const mapStateToProps = state => {
    return {
        materialsTypes: state.materialsTypes,
    };
}

const mapDispatchToProps = dispatch => {
    return {
        onUpdateEstablishmentSettings: (data, successCallback) => dispatch(updateEstablishmentSettings(data, successCallback)),
    };
};

export default connect(mapStateToProps, mapDispatchToProps)(EstablishmentDMModal);
