import React from 'react';
import { connect } from 'react-redux';
import { FormattedMessage } from 'react-intl';
import { withRouter } from "react-router";
import { logout } from '../../../actions/authentication/authentication';
import { updateLang } from '../../../actions/user/user';

class MenuProfile extends React.Component {

    logout(e) {
        e.preventDefault();
        e.stopPropagation();

        let that = this;
        function onLogoutSuccess() {
            that.props.history.push('/');
        };

        // Call logout and redirect on success
        this.props.onLogout(onLogoutSuccess);
    }

    updateLang(lang){
        this.props.onUpdateLang(lang);
    }

    render() {
        var lang = this.props.lang;

        if(!lang) return;

        // Available langs to chose from
        var availableLangs = ["en", "fr"];

        var otherLangsNode = availableLangs.map((l) => {
            // Lang is the one currently selected. Return
            if(l === lang) return null;

            // Add option to pick this lang
            return (
                <li key={l} className="dropdown-item" onClick={(e) => this.updateLang(l)}><FormattedMessage id={"Lang." + l} /></li>
            );
        });

        return (
            <div className="dropdown dropleft">
                <span className="" data-toggle="dropdown">
                    <span className="a-like">
                        <i className="fa fa-user-circle icon-big text-light d-none d-md-block"></i>
                        <i className="fa fa-user-circle icon-small text-light d-inline d-md-none"></i>
                        {this.props.user.first_name} {this.props.user.name}
                    </span>
                </span>
                <ul className="dropdown-menu multi-level">
                    <li className="dropdown-submenu">
                        <span className="dropdown-item"><FormattedMessage id="Lang" />&nbsp;: <FormattedMessage id={"Lang." + this.props.lang} /></span>
                        <ul className="dropdown-menu">
                            {otherLangsNode}
                        </ul>
                    </li>
                    <li className="dropdown-divider"></li>
                    <li className="dropdown-item" onClick={(e) => this.logout(e)}>Disconnect</li>
                </ul>
            </div>
        );
    }
}

const mapStateToProps = state => {
    return {
        lang: state.i18n.lang,
        user: state.user,
    }
};

const mapDispatchToProps = dispatch => {
    return {
        onLogout: (successCallback) => dispatch(logout(successCallback)),
        onUpdateLang: (lang) => dispatch(updateLang(lang))
    };
};


export default withRouter(connect(mapStateToProps, mapDispatchToProps)(MenuProfile));
