import React from 'react';
import { connect } from 'react-redux';
import {FormattedMessage, injectIntl} from 'react-intl';
import OrderRow from "./OrderRow";
import ConfirmationModal from '../sub/modals/ConfirmationModal';
import OrderStatus from '../../enums/OrderStatus';

class Orders extends React.Component {

    constructor(props){
        super(props);

        this.state = {
            modal: null,
            referenceFilter: null,
            dateFilter: null,
            statusFilter: null
        };
    }

    openConfModal(title, content, successCallback){
        this.setState({
            modal: <ConfirmationModal isOpen={true}
                title={title}
                content={content}
                successCallback={successCallback}
                closeModal={() => this.closeModal()} />
        });
    }

    closeModal(){
        this.setState({modal: null});
    }

    sortByDateDesc(order1, order2){
        if (order1.creation_date > order2.creation_date) return -1;
        if (order1.creation_date < order2.creation_date) return 1;
        return 0;
    }

    render() {
        if (this.props.orders.length === 0){
            return (
                <React.Fragment>
                    <a href="#/home/families" className="iconed-btn nostyle-a hoverable">
                        <span className="mt-3 mb-3">
                            <i className="fa fa-plus-circle icon-big text-dark"></i>
                            <p><FormattedMessage id="Order.Pass.Some" /></p>
                        </span>
                    </a>
                    <div className="alert alert-secondary" role="alert">
                        <FormattedMessage id="Empty.Orders" />
                    </div>
                </React.Fragment>
            );
        }

        let orders = this.props.orders.sort(this.sortByDateDesc);

        let ordersNode = orders.map((order) => {
            if(this.state.referenceFilter && this.state.referenceFilter !== ""){
                if(order.ref.indexOf(this.state.referenceFilter) === -1) return null;
            }

            if(this.state.dateFilter && this.state.dateFilter !== ""){
                if(new Date(order.creation_date) < new Date(this.state.dateFilter)) return null;
            }

            if(this.state.statusFilter && this.state.statusFilter !== ""){
                if(order.status !== Number(this.state.statusFilter)) return null;
            }

            return (
              <OrderRow
                  key={order._id}
                  order={order}
                  products={this.props.products}
                  openConfModal={(title, content, successCallback) => this.openConfModal(title, content, successCallback)}
                   />
            );
        });

        var statusNode = Object.values(OrderStatus).map(option => {
            return <option key={option} value={option}>{this.props.intl.formatMessage({ id: "Order.Status." + option })}</option>
        });

        return (
            <React.Fragment>
                {this.state.modal}

                <a href="#/home/families" className="iconed-btn nostyle-a hoverable">
                    <span className="mt-3 mb-3">
                        <i className="fa fa-plus-circle icon-big text-dark"></i>
                        <p><FormattedMessage id="Order.Pass.Some" /></p>
                    </span>
                </a>

                <table className="table table-striped-3 table-bordered">
                    <thead>
                        <tr>
                            <th className="d-none d-md-table-cell"><FormattedMessage id="Reference" /></th>
                            <th><FormattedMessage id="Date" /></th>
                            <th className="d-none d-lg-table-cell"><FormattedMessage id="Total" /> <FormattedMessage id="Excl.Tax" /></th>
                            <th><FormattedMessage id="Total" /> <FormattedMessage id="Incl.Tax" /></th>
                            <th><FormattedMessage id="Order.Status" /></th>
                            <th><FormattedMessage id="Actions" /></th>
                            <th className="d-none d-lg-table-cell"><FormattedMessage id="Urgent" /></th>
                        </tr>
                    </thead>
                    <tbody>

                        <tr className="d-none d-xl-table-row filter-row">
                            <td className="d-none d-md-table-cell"><input className="form-control" type="search" onChange={(e) => {this.setState({referenceFilter: e.target.value})}} /></td>
                            <td className="d-none d-md-table-cell"><input className="form-control" type="date" onChange={(e) => {this.setState({dateFilter: e.target.value})}} /></td>
                            <td className="d-none d-lg-table-cell"></td>
                            <td className="d-none d-md-table-cell"></td>
                            <td>
                                <select id="order-filter-status" className="form-control w-100" onChange={(e) => {this.setState({statusFilter: e.target.value})}}>
                                    <option value="">{this.props.intl.formatMessage({id: "All"})}</option>
                                    {statusNode}
                                </select>
                            </td>
                            <td></td>
                            <td className="d-none d-lg-table-cell"></td>
                        </tr>

                        {ordersNode}

                    </tbody>
                </table>
            </React.Fragment>
        );
    }
}

const mapStateToProps = state => {
    return {
        lang: state.i18n.lang,
        orders: state.orders,
        products: state.products,
    };
}

const mapDispatchToProps = dispatch => {
    return {
       //
    };
};

export default connect(mapStateToProps, mapDispatchToProps)(injectIntl(Orders));
