import React from 'react';
import { connect } from 'react-redux';
import PriceUtil from "../../../util/PriceUtil";

class OrderProductRowAdmin extends React.Component {

    constructor(props){
      super(props);

      this.state = {
        checked: false,
      };
    }

    updateDisponibility(e){
        this.setState({checked: !this.state.checked}, this.props.onClickCallback);
    }

    render() {

        return (
            <tr key={this.props.key} id={"product-" + this.props.product._id}>
              <td className="d-none d-lg-table-cell">{this.props.product.ref}</td>
              <td>{this.props.product.designation}</td>
              <td>{this.props.quantity}</td>
              <td className="d-none d-lg-table-cell">{PriceUtil.priceHt(this.props.product)}</td>
              <td className="d-none d-lg-table-cell">{PriceUtil.priceHt(this.props.product, this.props.quantity)}</td>
              <td className="text-center d-none d-lg-table-cell align-middle">
                  {(this.props.urgent) ? <i className={"fa fa-check icon-big text-info static"}></i> : <i className="fa fa-close icon-big text-muted op-3 static"></i>}
              </td>
              <td className="text-center card-body">
                  <div className="custom-control custom-switch mx-auto switch-success" onClick={(e) => this.updateDisponibility(e)}>
                      <input onChange={(e) => this.updateDisponibility(e)}
                          type="checkbox"
                          className={"custom-control-input switch-bg-blue disponibility"}
                          id={"switch-av-" + this.props.order._id + this.props.product._id }
                          value={this.props.product._id}
                          checked={this.state.checked}/>
                       <label className="custom-control-label" htmlFor={"switch-av-" + this.props.order._id + this.props.product._id}></label>
                  </div>
              </td>
            </tr>
        );
    }

}

const mapStateToProps = state => {
    return {
        user: state.user
    }
}

const mapDispatchToProps = dispatch => {
    return {
        //
    };
};

export default connect(mapStateToProps, mapDispatchToProps)(OrderProductRowAdmin);
