import React from 'react';
import { connect } from 'react-redux';
import { FormattedMessage } from 'react-intl';
import Util from '../../util/Util';

class EstablishmentBudgetModal extends React.Component {

    constructor(props) {
        super(props);
        
        this.START_YEAR = 2019;
        this.END_YEAR = 2025;
        this.YEARS = this.getYears(this.START_YEAR, this.END_YEAR);
        
        this.state = {
            year: this.START_YEAR,
            budget: 0,

            budgetError: null,
        };
    }

    onChange(field, value){
        if(field === "budget" && !Util.emptyString(value) && parseInt(value) >= 0){
            this.setState({budgetError: null});
        }

        this.setState({ [field]: value });
    }

    stopEvent(e) {
        e.stopPropagation();
    }

    getYears(startYear, endYear){
        var years = [];

        for(; startYear <= endYear; startYear++){
            years.push(startYear);
        }

        return years;
    }

    onComplete(){
        // Value is empty. Don't go further
        if(Util.emptyString(this.state.budget)){
            this.setState({ budgetError: <FormattedMessage id="Must.Provide.Value" /> });
            return;
        }
        if(parseInt(this.state.budget) < 0){
            this.setState({ budgetError: <FormattedMessage id="Must.Provide.Positive.Value" /> });
            return;
        }

        // If there is already a budget object
        if(this.props.budgets && this.props.budgets.length !== 0){
            var alreadyIn = false;
            for(let budget of this.props.budgets){
                if(parseInt(budget.year) === parseInt(this.state.year)){
                    budget.budget = this.state.budget;
                    alreadyIn = true;
                    break;
                }
            }

            if(!alreadyIn) this.props.budgets.push({year: this.state.year, budget: this.state.budget});

            this.props.onComplete(this.props.budgets);
        }
        // Else, add new budget
        else{
            this.props.onComplete([{year: this.state.year, budget: this.state.budget}]);
        }

    }

    hasYear(year, budgets){
        if(Util.emptyArray(budgets)) return false;

        for(let budget of budgets){
            if(parseInt(year) === parseInt(budget.year)) return true;
        }

        return false;
    }

    getBudget(year){
        for(let budget of this.props.budgets){
            if(parseInt(year) === parseInt(budget.year)) return budget.budget;
        }
    }

    getDefaultYear(){
        for(let year of this.YEARS){
            if(!this.hasYear(year, this.props.budgets)) return year;
        }
    }

    componentWillMount(){
        let budget = 0;
        let year = this.getDefaultYear();

        // Modal has been opened to modify a budget: display values
        if(this.props.year){
            budget = this.getBudget(this.props.year);
            year = this.props.year;
        }

        this.setState({year: year, budget: budget});
    }

    render() {
        var yearsOptionsNode = this.YEARS.map((year) => {
            let disabled = this.hasYear(year, this.props.budgets);
            return <option key={year} value={year} disabled={disabled}>{year}</option>;
        });

        var title = <FormattedMessage id="Add.Annual.Budget" />;
        if(this.props.year) title = <FormattedMessage id="Modify.Annual.Budget" />;

        return (
            <div className="modal-bg show" onClick={() => this.props.close()}>
                <div className="modal fade show">
                    <div className="modal-dialog" role="document" onClick={(e) => this.stopEvent(e)}>
                        <div className="modal-content">
                            <div className="modal-header">
                                <h5 className="modal-title" id="exampleModalLabel">{title}</h5>
                                <button type="button" className="close" data-dismiss="modal" aria-label="Close" onClick={() => this.props.close()}>
                                    <span aria-hidden="true">&times;</span>
                                </button>
                            </div>

                            <div className="modal-body">

                                <div className="form-group row">
                                    <label htmlFor="year" className="col-5 col-form-label"><FormattedMessage id="Year" /></label>
                                    <div className="col-7">
                                    <select id="year" className="form-control" defaultValue={this.state.year} 
                                        disabled={this.props.year}
                                        onChange={(e) => this.onChange("year", e.target.value)}>
                                        {yearsOptionsNode}
                                    </select>
                                    </div>
                                </div>

                                <div className="form-group row">
                                    <label htmlFor="budget" className="col-5 col-form-label"><FormattedMessage id="Budget" /></label>
                                    <div className="col-7">
                                        <input id="budget" type="number" className="form-control" value={this.state.budget} 
                                            onChange={(e) => this.onChange("budget", e.target.value)}
                                            onBlur={() => this.onComplete()} />
                                        <div className="text-danger"><small>{this.state.budgetError}</small></div>
                                    </div>
                                </div>

                            </div>

                            {(!this.props.year) && 
                                <div className="modal-footer">
                                    <button type="button" className="btn btn-secondary" data-dismiss="modal" onClick={() => this.props.close()}><FormattedMessage id="Cancel" /></button>
                                    <button type="button" className="btn btn-primary" onClick={() => this.onComplete()}><FormattedMessage id="Create" /></button>
                                </div>
                            }
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}

const mapStateToProps = state => {
    return {
        //
    };
}

const mapDispatchToProps = dispatch => {
    return {
        //
    };
};

export default connect(mapStateToProps, mapDispatchToProps)(EstablishmentBudgetModal);