import React from 'react';
import { connect } from 'react-redux';
import { FormattedMessage, FormattedHTMLMessage } from 'react-intl';
import ConfirmationModal from "../sub/modals/ConfirmationModal";

class EstablishmentBudgets extends React.Component {

    constructor(props) {
        super(props);

        this.state = {
            modal: null,
        };
    }

    sortByDateAsc(budget1, budget2){
        if (budget1.year < budget2.year) return -1;
        if (budget1.year > budget2.year) return 1;
        return 0;
    }
    
    deleteBudget(year){
        var sendUpdatedBudget = () => {
            for(var i=0; i<this.props.budgets.length; i++){
                if(parseInt(this.props.budgets[i].year) === parseInt(year)){
                    this.props.budgets.splice(i, 1);
                    break;
                }
            }
    
            this.props.updateBudgets(this.props.budgets);
        };

        this.setState({
            modal: <ConfirmationModal isOpen={true}
                content={<FormattedHTMLMessage id="Remove.Row.Confirmation" />}
                successCallback={() => sendUpdatedBudget()}
                closeModal={() => this.closeModal()} />
        });
    }

    closeModal(){
        this.setState({ modal: null });
    }

    editBudget(year){
        this.props.editBudget(year);
    }

    render() {
        let budgets = this.props.budgets.sort(this.sortByDateAsc);

        let budgetsNode = budgets.map((budget) => {
            return (
                <tr key={budget.year}>
                    <th>{budget.year}</th>
                    <th>{budget.budget}</th>
                    <td className="text-center">
                        <i className="fa fa-edit icon-big" onClick={() => this.editBudget(budget.year)}></i>
                        <i className="fa fa-trash icon-big" onClick={(e) => this.deleteBudget(budget.year)}></i>
                    </td>
                </tr>
            );

        });

        return (
            <React.Fragment>
                {this.state.modal}
                <table className="table table-striped table-bordered col-12">
                    <thead>
                        <tr>
                            <th><FormattedHTMLMessage id="Year" /></th>
                            <th><FormattedHTMLMessage id="Budget" /></th>
                            <th><FormattedMessage id="Actions" /></th>
                        </tr>
                    </thead>
                    <tbody>
                        {budgetsNode}
                    </tbody>
                </table>
                {this.state.modal}
            </React.Fragment>
        );
    }

}

const mapStateToProps = state => {
    return {
        lang: state.user.lang,
    }
}

const mapDispatchToProps = dispatch => {
    return {

    };
};

export default connect(mapStateToProps, mapDispatchToProps)(EstablishmentBudgets);
