import React from 'react';
import { connect } from 'react-redux';
import PriceUtil from "../../util/PriceUtil";
import Roles from "../../enums/Roles";

class OrderProductRow extends React.Component {

    render() {
        let urgentIconClassName = "text-success";
        if(this.props.user.role === Roles.ADMIN) urgentIconClassName = "text-info";

        return (
            <tr key={this.props.key} id={"product-" + this.props.product._id}>
              <td className="d-none d-md-table-cell">{this.props.product.ref}</td>
              <td>{this.props.product.designation} {(this.props.urgent) && <i className={"fa fa-check " + urgentIconClassName + " float-right"}></i> }</td>
              <td>{this.props.quantity}</td>
              <td className="d-none d-md-table-cell">{PriceUtil.priceHt(this.props.product)}</td>
              <td>{PriceUtil.priceHt(this.props.product, this.props.quantity)}</td>
            </tr>
        );
    }

}

const mapStateToProps = state => {
    return {
        user: state.user
    }
}

const mapDispatchToProps = dispatch => {
    return {
        //
    };
};

export default connect(mapStateToProps, mapDispatchToProps)(OrderProductRow);
