import Maths from "./Maths";

export default class PriceUtil{

    constructor(){
        this.store = []; // {product, quantity}
    }

    // =====================================================
    // ================ IN / OUT OPERATIONS ================
    // =====================================================

    add(product, quantity){
        this.store.push({product: product, quantity: quantity});
    }

    reset(){
        this.store = [];
    }

    // =====================================================
    // ============ SINGLE PRODUCT OPERATIONS ==============
    // =====================================================

    static priceHt(product, quantity=1){
        return Maths.round(Maths.round(product.prix_u_ht_emera) * quantity);
    }

    static priceTtc(product, quantity=1){
        return Maths.round(Maths.round(this.priceHt(product) * (1 + this.tva(product))) * quantity);
    }

    static tva(product, quantity=1){
        return Maths.round(Maths.round(parseFloat(product.tva)) * quantity);
    }

    // =====================================================
    // ================== TOTAL OPERATIONS =================
    // =====================================================

    totalHt(){
        var totalHt = 0;

        for(let s of this.store){
            totalHt = Maths.round(totalHt + PriceUtil.priceHt(s.product) * s.quantity);
        }

        return totalHt;
    }

    totalTtc(){
        var totalTtc = 0;

        for(let s of this.store){
            totalTtc = Maths.round(totalTtc + PriceUtil.priceTtc(s.product) * s.quantity);
        }

        return totalTtc;
    }

    totalTva(){
        var totalTva = 0;

        for(let s of this.store){
            totalTva = Maths.round(totalTva + Maths.round(PriceUtil.priceHt(s.product) * PriceUtil.tva(s.product)) * s.quantity);
        }

        return totalTva;
    }

}
