    checkIfEmailExists:  rootUrl + "/user/check/email/",                               // GET {email}

    // Products
    getProducts:         rootUrl + "/product",                                         // GET
    getProductImg:       rootUrl + "/product/img/",                                    // GET {mercurialId, ref}
