import React from 'react';
import { connect } from 'react-redux';
import MercurialFileModal from "./MercurialFileModal";
import MercurialColumnsModal from "./MercurialColumnsModal";
import MercurialImgsModal from "./MercurialImgsModal";
import MercurialUpdModal from "./MercurialUpdModal";
import {addMercurial, addProductsImgs, addFamilyImgs, updateMercurial} from "../../actions/mercurials/mercurials";
import MercurialRow from './MercurialRow';
import ExcelUtil from '../../util/ExcelUtil';
import ConfirmationModal from '../sub/modals/ConfirmationModal';
import ErrorModal from '../sub/modals/ErrorModal';
import StringUtil from '../../util/StringUtil';

import {FormattedMessage} from 'react-intl';
import MercurialsMapping from '../../util/MercurialsMapping';

class Mercurials extends React.Component {

    constructor(props){
        super(props);

        this.state = {
            modal: null,
            // Import data
            mercurialName: null,
            fileName: null,
            fileData: null,
            startDate: null,
            endDate: null,
            columns: null,
            // Filters
            nameFilter: null,
            startDateFilter: null,
            endDateFilter: null,
        };
    }

    openMercurialImportModal(e){
        e.preventDefault();
        e.stopPropagation();

        this.setState({
            modal: <MercurialFileModal closeModal={() => this.closeModal()} 
                onComplete={(mercurialName, fileData, startDate, endDate) => this.onCompleteStep1(mercurialName, fileData, startDate, endDate)} />
        });
    }

    onCompleteStep1(mercurialName, file, startDate, endDate){
        // Parse the excel file and then move on to next modal
        ExcelUtil.parse(file, (fileData) => {
            // Save the passed params
            this.setState({
                mercurialName: mercurialName,
                fileName: file.name,
                fileData: fileData,
                startDate: startDate,
                endDate: endDate
            });
    
            // Close current modal
            this.closeModal();

            // Check for corrupt data
            if(this.isCorrupt(fileData)){
                this.openCorruptFileModal();
            }else{
                // Open next step modal
                this.setState({
                    modal: <MercurialColumnsModal 
                        closeModal={() => this.closeModal()} 
                        fileData={fileData}
                        onComplete={(columns) => this.onCompleteStep2(columns)} />
                });
            }
        });
    }

    isCorrupt(json){
        let nbrKeys = 0;
        for (let row of json) nbrKeys = Math.max(nbrKeys, Object.keys(row).length);

        for (let row of json) {
            // Keys are missing. That's a no-no
            if (Object.keys(row).length !== nbrKeys) return true;

            for (let key of Object.keys(row)) {
                // Empty values are not accepted. Throw
                if (row[key] === undefined || row[key] === null || row[key] === "") return true;
            }
        }

        return false;
    }
    
    openCorruptFileModal(){
        var errorModalTitle = <FormattedMessage id="Error" />;
        var errorModalContent = <FormattedMessage id="Mercurial.File.Corrupt" />;

        this.setState({
            mercurialName: null,
            fileName: null,
            fileData: null,
            startDate: null,
            endDate: null,
            columns: null,
            modal: <ErrorModal 
                isOpen={true}
                title={errorModalTitle}
                content={errorModalContent}
                closeModal={() => this.closeModal()} />
        });
    }

    onCompleteStep2(columns){
        this.setState({
            columns: columns
        });

        var successCallback = (mercurial) => {
            // Close current modal
            this.closeModal();

            this.openImgsModal(mercurial);
        };

        // Send the mercurial to the BE
        this.addMercurial(this.state.mercurialName, this.state.fileName, this.state.fileData, this.state.startDate, this.state.endDate, columns, successCallback);
    }

    addMercurial(name, fileName, data, startDate, endDate, columns, callback) {
        // First, fix data with correct columns
        var products = this.fixProductsCols(data, columns);

        // Then, send data to the BE
        let mercurial = {name: name, fileName: fileName, startDate: startDate, endDate: endDate, products: products};
        this.props.onaddMercurial(mercurial, callback);
    }

    fixProductsCols(products, columns){
        let newProducts = [];
        for(let p of products){
            var newProduct = {};
            for(let col of Object.keys(columns)) newProduct[col] = p[columns[col]];
            newProducts.push(newProduct);
        }

        return newProducts;
    }

    openConfModal(title, content, successCallback){
        this.setState({
            modal: <ConfirmationModal isOpen={true}
                title={title}
                size="xl"
                content={content}
                successCallback={successCallback}
                closeModal={() => this.closeModal()} />
        });
    }
    
    openImgsModal(mercurial){
        this.setState({
            modal: <MercurialImgsModal 
                closeModal={() => this.closeModal()} 
                onComplete={(mercurialId, productImgs, familyImgs) => this.uploadImgs(mercurialId, productImgs, familyImgs)} 
                mercurial={mercurial} />
        });
    }

    uploadImgs(mercurialId, productImgs, familyImgs){
        var sendProductImgs = (successCallback) => {
            if(!productImgs || productImgs.length === 0) return successCallback();

            let formData = new FormData();
            for(let img of productImgs) formData.append(img.name, img);

            this.props.onAddProductsImgs(mercurialId, formData, successCallback);
        };

        var sendFamilyImgs = (successCallback) => {
            if(!familyImgs || familyImgs.length === 0) return successCallback();

            let formData = new FormData();
            for(let img of familyImgs) formData.append(img.name, img);

            this.props.onAddFamilyImgs(mercurialId, formData, successCallback);
        };

        sendProductImgs(
            () => sendFamilyImgs(
                () => this.closeModal()
            )
        );
    }

    closeModal(){
        this.setState({modal: null});
    }

    openMercurialUpdModal(mercurial){
        this.setState({
            modal: <MercurialUpdModal 
            mercurial={mercurial}
            closeModal={() => this.closeModal()} 
            onComplete={(mercurial, file) => this.onUpd(mercurial, file)} />
        });
    }
    
    onUpd(mercurial, file){
        // Parse the excel file and then move on to next modal
        ExcelUtil.parse(file, (fileData) => {
            // Check for corrupt data
            if(this.isCorrupt(fileData)){
                // Close current modal
                this.closeModal();

                this.openCorruptFileModal();
            }else{
                if(!fileData[0]){
                    this.closeModal();
                    return;
                }

                // Auto-match columns
                let columns = {};
                for(let key of Object.keys(MercurialsMapping)){
                    let closest = StringUtil.closest(MercurialsMapping[key], Object.keys(fileData[0]));

                    if(!closest){
                        this.openCorruptFileModal();
                        return;
                    } 

                    columns[key] = closest;
                }

                // Fix products keys with auto-matching
                var products = this.fixProductsCols(fileData, columns);
                
                // Send to BE
                this.props.onUpdateMercurial({mercurialId: mercurial._id, fileName: file.name, products: products}, () => this.closeModal());
            }
        });
    }

    render() {
        if(this.props.mercurials.length === 0){
            return (
                <React.Fragment>
                    {this.state.modal}
    
                    <span onClick={(e) => this.openMercurialImportModal(e)} className="iconed-btn hoverable clickable">
                        <span className="mt-3 mb-3">
                            <i className="fa fa-plus-circle icon-big text-dark"></i>
                            <p><FormattedMessage id="Mercurials.Add.Some" /></p>
                        </span>
                    </span>
                    <div className="alert alert-secondary" role="alert">
                        <FormattedMessage id="Empty.Mercurials" />
                    </div>
                </React.Fragment>
            );
        }

        var mercurialsNode = this.props.mercurials.map((mercurial) => {
            if(this.state.nameFilter && this.state.nameFilter !== ""){
                if(mercurial.name.indexOf(this.state.nameFilter) === -1) return null;
            }

            if(this.state.startDateFilter && this.state.startDateFilter !== ""){
                if(new Date(mercurial.start_date) < new Date(this.state.startDateFilter)) return null;
            }

            if(this.state.endDateFilter && this.state.endDateFilter !== ""){
                if(new Date(mercurial.end_date) < new Date(this.state.endDateFilter)) return null;
            }

            return <MercurialRow 
                key={mercurial.name + "-" + mercurial.version} 
                mercurial={mercurial}
                mercurials={this.props.mercurials}
                openMercurialUpdModal={(mercurial) => this.openMercurialUpdModal(mercurial)}
                openConfModal={(title, content, successCallback) => this.openConfModal(title, content, successCallback)}
                openImgsModal={(mercurial) => this.openImgsModal(mercurial)}
                />;
        });

        return (
            <React.Fragment>
                <span onClick={(e) => this.openMercurialImportModal(e)} className="iconed-btn hoverable clickable">
                    <span className="mt-3 mb-3">
                        <i className="fa fa-plus-circle icon-big text-dark"></i>
                        <p><FormattedMessage id="Mercurials.Add.Some" /></p>
                    </span>
                </span>

                <table className="table table-striped table-bordered">
                    <thead>
                        <tr>
                            <th><FormattedMessage id="Name" /></th>
                            <th className="d-none d-md-table-cell"><FormattedMessage id="Start.Date" /></th>
                            <th className="d-none d-md-table-cell"><FormattedMessage id="End.Date" /></th>
                            <th className="d-none d-sm-table-cell"><FormattedMessage id="File" /></th>
                            <th className="d-none d-md-table-cell"><FormattedMessage id="Version" /></th>
                            <th><FormattedMessage id="Actions" /></th>
                        </tr>
                    </thead>
                    <tbody>

                        <tr className="d-none d-xl-table-row filter-row">
                            <td><input className="form-control" type="search" onChange={(e) => {this.setState({nameFilter: e.target.value})}} /></td>
                            <td className="d-none d-md-table-cell"><input className="form-control" type="date" onChange={(e) => {this.setState({startDateFilter: e.target.value})}} /></td>
                            <td className="d-none d-lg-table-cell"><input className="form-control" type="date" onChange={(e) => {this.setState({endDateFilter: e.target.value})}} /></td>
                            <td className="d-none d-sm-table-cell"></td>
                            <td className="d-none d-md-table-cell"></td>
                            <td></td>
                        </tr>

                        {mercurialsNode}

                    </tbody>
                </table>

                {this.state.modal}

            </React.Fragment>
        );

    }
}

const mapStateToProps = state => {
    return {
        mercurials: state.mercurials
    }
}

const mapDispatchToProps = dispatch => {
    return {
        onaddMercurial: (data, successCallback) => dispatch(addMercurial(data, successCallback)),
        onAddProductsImgs: (mercurialId, data, successCallback) => dispatch(addProductsImgs(mercurialId, data, successCallback)),
        onAddFamilyImgs: (mercurialId, data, successCallback) => dispatch(addFamilyImgs(mercurialId, data, successCallback)),
        onUpdateMercurial: (data, successCallback) => dispatch(updateMercurial(data, successCallback)),
    };
};

export default connect(mapStateToProps, mapDispatchToProps)(Mercurials);

