import React from 'react';
import { connect } from 'react-redux';
import PriceUtil from "../../../util/PriceUtil";
import { FormattedMessage } from 'react-intl';
import OrderProductRowAdmin from "./OrderProductRowAdmin";

class OrderProductsAdmin extends React.Component {

    getProduct(productId) {
        // Product from regular mercurial
        for (let product of this.props.products) {
            if (product._id === productId) return product;
        }

        // Product from outdated mercurial
        for(let oldProduct of this.props.oldProducts){
            if (oldProduct._id === productId) return oldProduct;
        }
    }

    render() {
        if (this.props.products.length === 0) return null;

        // Helper class for prices calculations
        var priceUtil = new PriceUtil();

        let key = 0;
        let productNode = this.props.order.products.map((p) => {
            var product = this.getProduct(p.id_product);
            priceUtil.add(product, p.quantity);
            return (
                <OrderProductRowAdmin
                    key={++key + "-" + product._id}
                    order={this.props.order}
                    product={product}
                    quantity={p.quantity}
                    urgent={p.urgent}
                    onClickCallback={this.props.onClickCallback}
                    />
            );
        });

        return (
            <table id="bootstrap-data-table-export" className="table table-striped table-bordered m-0">
                <thead>
                    <tr>
                        <th className="d-none d-lg-table-cell"><FormattedMessage id="Reference" /></th>
                        <th><FormattedMessage id="Designation" /></th>
                        <th><FormattedMessage id="Quantity" /></th>
                        <th className="d-none d-lg-table-cell"><FormattedMessage id="Unit.Price.Excl.Tax" /></th>
                        <th className="d-none d-lg-table-cell"><FormattedMessage id="Total.Excl.Tax" /></th>
                        <th className="d-none d-lg-table-cell"><FormattedMessage id="Urgent" /></th>
                        <th><FormattedMessage id="Actions" /></th>
                    </tr>
                </thead>
                <tbody>
                    {productNode}
                </tbody>
            </table>
        );
    }

}

const mapStateToProps = state => {
    return {
        lang: state.i18n.lang,
        products: state.products,
        oldProducts: state.oldProducts,
    }
}

const mapDispatchToProps = dispatch => {
    return {
        //
    };
};

export default connect(mapStateToProps, mapDispatchToProps)(OrderProductsAdmin);
