import React from 'react';
import { connect } from 'react-redux';
import { FormattedMessage, injectIntl } from 'react-intl';
import ComboxBox from "../sub/ComboBox";
import { addPaymentMethod } from '../../actions/configurables/paymentMethods';
import { addPaymentDeadline } from '../../actions/configurables/paymentDeadlines';
import { updateEstablishmentSettings } from '../../actions/establishments/establishmentSettings';
import Util from '../../util/Util';
import EstablishmentBudgetModal from './EstablishmentBudgetModal';
import EstablishmentBudgets from './EstablishmentBudgets';

class EstablishmentConfiguration extends React.Component {

    constructor(props) {
        super(props);

        this.state = {
            payment_method_id: "",
            payment_deadline_id: "",
            mercurial_id: "",
            shipping_delay: "",
            min_order_amount: "",

            modal: null,
            disabled: false
        };
    }

    componentWillMount() {
        this.fixWithDefaultValues();
    }

    fixWithDefaultValues() {
        if (!this.props.establishmentSettings) return;

        for (let key of Object.keys(this.state)) {
            if (Util.emptyString(this.state[key]) && !Util.emptyString(this.props.establishmentSettings[key])) {
                this.setState({ [key]: this.props.establishmentSettings[key] });
            }
        }
    }

    onFieldChange(field, value) {
        this.setState({ [field]: value });
    }

    sendUpdate(field, value) {
        var data = {
            establishmentId: this.props.establishment._id,
            updatedField: field,
            updatedValue: value
        };

        // Send to BE
        this.props.onUpdateEstablishmentSettings(data, () => this.fixWithDefaultValues());
    }

    openAnnualBudgetModal(year) {
        var budgets = (this.props.establishmentSettings) ? this.props.establishmentSettings.budgets : null;

        this.setState({
            modal: <EstablishmentBudgetModal
                budgets={budgets}
                year={year}
                close={() => this.closeModal()}
                onComplete={(budgets) => {
                    this.sendUpdate("budgets", budgets);
                    this.closeModal();
                }} />
        });
    }

    closeModal() {
        this.setState({
            modal: null
        });
    }

    render() {
        var now = new Date(Date.now());
        let mercurialSelectOptions = this.props.mercurials.map((mercurial) => {
            if(new Date(mercurial.start_date) > now || new Date(mercurial.end_date) < now) return null;
            
            let version = null;
            if(mercurial.version !== 1) version = "(v" + mercurial.version + ")";

            return (<option key={mercurial._id} value={mercurial._id}>{mercurial.name} {version} </option>);
        });

        let budgets = null;
        if (this.props.establishmentSettings && this.props.establishmentSettings.budgets && this.props.establishmentSettings.budgets.length > 0) {
            budgets = (
                <EstablishmentBudgets
                    budgets={this.props.establishmentSettings.budgets}
                    editBudget={(year) => this.openAnnualBudgetModal(year)}
                    updateBudgets={(budgets) => this.sendUpdate("budgets", budgets)} />
            );
        }

        return (
            <React.Fragment>

                <div className="row mb-3">

                    <div className="col-12 col-lg-12 col-xl-9">

                        <div className="form-group row">
                            <label htmlFor="conf-payment-method" className="col-12 col-md-7 col-form-label"><FormattedMessage id="Payment.Method" /></label>
                            <div className="col-12 col-md-5">
                                <ComboxBox
                                    menuPlacement="auto"
                                    onChange={(value) => this.onFieldChange("payment_method_id", value)}
                                    onBlur={(value) => this.sendUpdate("payment_method_id", value)}
                                    defaultOption={this.state.payment_method_id}
                                    options={this.props.paymentMethods}
                                    onCreateOption={(data, successCallback) => this.props.onAddPaymentMethod(data, successCallback)} />
                            </div>
                        </div>

                        <div className="form-group row">
                            <label htmlFor="conf-payment-deadline" className="col-12 col-md-7 col-form-label"><FormattedMessage id="Payments.Deadline" /></label>
                            <div className="col-12 col-md-5">
                                <ComboxBox
                                    menuPlacement="auto"
                                    onChange={(value) => this.onFieldChange("payment_deadline_id", value)}
                                    onBlur={(value) => this.sendUpdate("payment_deadline_id", value)}
                                    defaultOption={this.state.payment_deadline_id}
                                    options={this.props.paymentDeadlines}
                                    onCreateOption={(data, successCallback) => this.props.onAddPaymentDeadline(data, successCallback)} />
                            </div>
                        </div>

                        <div className="form-group row">
                            <label htmlFor="conf-mercurial" className="col-12 col-md-7 col-form-label"><FormattedMessage id="Mercurial" /></label>
                            <div className="col-12 col-md-5">
                                <select id="conf-mercurial" className="form-control w-100"
                                    defaultValue={this.state.mercurial_id}
                                    onChange={(e) => this.onFieldChange("mercurial_id", e.target.value)}
                                    onBlur={(e) => this.sendUpdate("mercurial_id", e.target.value)}>
                                    <option value="">{this.props.intl.formatMessage({ id: "Select" })}</option>
                                    {mercurialSelectOptions}
                                </select>
                            </div>
                        </div>

                        <div className="form-group row">
                            <label htmlFor="shippingDelay" className="col-12 col-md-7 col-form-label"><FormattedMessage id="Shipping.Delay" /></label>
                            <div className="col-12 col-md-5">
                                <input type="number" className="form-control" id="shippingDelay" min="0" value={this.state.shipping_delay}
                                    onChange={(e) => this.onFieldChange('shipping_delay', e.target.value)}
                                    onBlur={(e) => this.sendUpdate('shipping_delay', e.target.value)} />
                            </div>
                        </div>

                        <div className="form-group row">
                            <label htmlFor="minPrice" className="col-12 col-md-7 col-form-label"><FormattedMessage id="Min.Price" /></label>
                            <div className="col-12 col-md-5">
                                <input type="number" className="form-control" id="minPrice" min="0" value={this.state.min_order_amount}
                                    onChange={(e) => this.onFieldChange('min_order_amount', e.target.value)}
                                    onBlur={(e) => this.sendUpdate('min_order_amount', e.target.value)} />
                            </div>
                        </div>

                        <div className="form-group row">
                            <label htmlFor="shippingCost" className="col-10 col-sm-11 col-md-7 col-form-label"><FormattedMessage id="Annual.Budgets" /></label>

                            <div className="col-2 col-sm-1 col-md-5">
                                <i className="fa fa-plus-square icon-big hoverable clickable p-0 pt-2"  onClick={(e) => this.openAnnualBudgetModal()}></i>
                            </div>
                        </div>

                        {budgets}

                    </div>

                </div>

                {this.state.modal}

            </React.Fragment>
        );
    }
};

const mapStateToProps = state => {
    return {
        paymentMethods: state.paymentMethods,
        paymentDeadlines: state.paymentDeadlines,
        mercurials: state.mercurials
    };
};

const mapDispatchToProps = dispatch => {
    return {
        onAddPaymentMethod: (data, successCallback) => dispatch(addPaymentMethod(data, successCallback)),
        onAddPaymentDeadline: (data, successCallback) => dispatch(addPaymentDeadline(data, successCallback)),
        onUpdateEstablishmentSettings: (data, successCallback) => dispatch(updateEstablishmentSettings(data, successCallback)),
    };
};

export default connect(mapStateToProps, mapDispatchToProps)(injectIntl(EstablishmentConfiguration));

