import React from 'react';
import { connect } from 'react-redux';
import { FormattedMessage } from 'react-intl';
import {addMaterialsType} from "../../../../actions/settings/materialsTypes/materialsTypes";


class MaterialsTypesModalAdmin extends React.Component {

    constructor(props) {
        super(props);

        this.state = {
            name: "",
            nameError: null,
            disabled: false,
        };
    }

    close() {
        this.props.closeModal();
    }

    onChange(value) {
        // Check that this material type does not already exist
        for (var materialTypeName of this.props.materialsTypes) {
            if (materialTypeName.name.toUpperCase() === value.toUpperCase().trim()) {
                this.setState({
                    name: value,
                    nameError: <FormattedMessage id="Material.Exist" />,
                    disabled: true
                });

                return;
            }
        }

        this.setState({
            name: value,
            nameError: null,
            disabled: false
        });
    }

    add() {
        this.setState({disabled: true});

        var data = {newMaterial: this.state.name.trim()};

        this.props.onAddMaterialsType(data, () => this.close());
    }

    stopEvent(e) {
        e.preventDefault();
        e.stopPropagation();
    }

    render() {

        return (
            <div className="modal-bg show" onClick={() => this.close()}>
                <div className="modal fade show">
                    <div className="modal-dialog" role="document" onClick={(e) => this.stopEvent(e)}>
                        <div className="modal-content">

                            <div className="modal-header">
                                <h5 className="modal-title" id="exampleModalLabel"><FormattedMessage id="Add.Material" /></h5>
                                <button type="button" className="close" data-dismiss="modal" aria-label="Close" onClick={() => this.close()}>
                                    <span aria-hidden="true">&times;</span>
                                </button>
                            </div>

                            <div className="modal-body">

                                <div className="form-group row">
                                    <label htmlFor="name" className="col-sm-4 col-form-label"><FormattedMessage id="Name" /></label>
                                    <div id="name" className="col-sm-8">
                                        <input className="form-control" type="text" id="materialName" name="name" onChange={(e) => this.onChange(e.target.value)} />
                                        <small className="text-danger">{this.state.nameError}</small>
                                    </div>
                                </div>

                            </div>

                            <div className="modal-footer">
                                <button type="button" className="btn btn-secondary" data-dismiss="modal" onClick={() => this.close()}><FormattedMessage id="Cancel" /></button>
                                <button type="button" className="btn btn-primary" onClick={() => this.add()}
                                    disabled={this.state.disabled || this.state.nameError || this.state.name === ""}><FormattedMessage id="Add" /></button>
                            </div>

                        </div>
                    </div>
                </div>
            </div>
        );
    }
}

const mapStateToProps = state => {
    return {
        materialsTypes: state.materialsTypes,
    }
}

const mapDispatchToProps = dispatch => {
    return {
        onAddMaterialsType: (data, successCallback) => dispatch(addMaterialsType(data, successCallback))
    };
};

export default connect(mapStateToProps, mapDispatchToProps)(MaterialsTypesModalAdmin);
