import React from 'react';
import { connect } from 'react-redux';
import { FormattedMessage } from 'react-intl';
import { withRouter } from "react-router";
import MenuProfile from './MenuProfile';

class Menu extends React.Component {

    render() {
        return (
            <nav className="navbar navbar-expand-md navbar-dark bg-custom-green">

                <div id="menu" className="collapse navbar-collapse mb-3 mb-md-0">

                    <ul className="navbar-nav text-center">

                        <a href="#/home">
                            <li className="nav-item mx-2 p-2">
                                <i className="fa fa-home icon-big text-light d-none d-md-block"></i>
                                <i className="fa fa-home icon-small text-light d-inline d-md-none"></i>
                                <FormattedMessage id="Home" />
                            </li>
                        </a>

                        <a href="#/home/orders">
                            <li className="nav-item mx-2 p-2">
                                <i className="fa fa-clipboard icon-big text-light d-none d-md-block"></i>
                                <i className="fa fa-clipboard icon-small text-light d-inline d-md-none"></i>
                                <span className="relative">
                                    <FormattedMessage id="Orders" />
                                    {this.props.orders.length > 0 && <span className="count bg-warning">{this.props.orders.length}</span>}
                                </span>
                            </li>
                        </a>

                        <a href="#/home/fleet">
                            <li className="nav-item mx-2 p-2">
                                <i className="fa fa-building icon-big text-light d-none d-md-block"></i>
                                <i className="fa fa-building icon-small text-light d-inline d-md-none"></i>
                                <span className="relative">
                                    <FormattedMessage id="Fleet" />
                                </span>
                            </li>
                        </a>

                        <a href="#/home/cart" className="ml-md-auto">
                            <li className="nav-item mx-2 p-2">
                                <i className="fa fa-shopping-cart icon-big text-light d-none d-md-block"></i>
                                <i className="fa fa-shopping-cart icon-small text-light d-inline d-md-none"></i>
                                <span className="relative">
                                    <FormattedMessage id="Cart" />
                                    {this.props.cart.length > 0 && <span className="count bg-warning">{this.props.totalProduct}</span>}
                                </span>
                            </li>
                        </a>

                        <li className="nav-item mx-2 p-2">
                            <MenuProfile />
                        </li>

                    </ul>

                </div>

                <button className="navbar-toggler m-auto" type="button" data-toggle="collapse" data-target="#menu" aria-controls="menu" aria-expanded="false" aria-label="Toggle navigation">
                    <span className="navbar-toggler-icon"></span>
                </button>

            </nav>
        );
    }
}

const mapStateToProps = state => {
    return {
        lang: state.i18n.lang,
        cart: state.cart,
        orders: state.orders
    }
};

const mapDispatchToProps = dispatch => {
    return {
        //
    };
};


export default withRouter(connect(mapStateToProps, mapDispatchToProps)(Menu));
